import { DrawingEngine } from '../';
import { GeometryBase } from './Base/GeometryBase';
export class GeometryCommon{
    drawingEngine: DrawingEngine;
    constructor(drawingEngine: DrawingEngine){
        this.drawingEngine = drawingEngine;

    }
    public find(x: number, y: number): GeometryBase | undefined {
        for (let key in this.drawingEngine.scene.geoDict) {
            if (this.drawingEngine.scene.geoDict[key].isInside(x, y)) {
                return this.drawingEngine.scene.geoDict[key];
            }
        }
        return undefined
    }
    public resetSelections(){
        for (let i = 0 ; i < this.drawingEngine.scene.geometryList.length; ++i) {
            this.drawingEngine.scene.geometryList[i].isSelected = false;
        }
    }
    public getAllVecFromCPoint(){
        const vs = [];
        for (let i = 0 ; i < this.drawingEngine.scene.pointList.length; ++i) {
            vs.push(this.drawingEngine.scene.pointList[i].v);
        }
        return vs;
    }
    public getAllFromCPoint(){
        const vs = [];
        for (let i = 0 ; i < this.drawingEngine.scene.pointList.length; ++i) {
            vs.push(this.drawingEngine.scene.pointList[i]);
        }
        return vs;
    }
    public getSelCPoint(){
        const vs = [];
        for (let i = 0 ; i < this.drawingEngine.scene.pointList.length; ++i) {
            if (this.drawingEngine.scene.pointList[i].isSelected) {
                vs.push(this.drawingEngine.scene.pointList[i]);
            }
        }
        return vs;
    }
    public updateHover(x: number, y: number) {
        for (let i = 0 ; i < this.drawingEngine.scene.geometryList.length; ++i) {
            this.drawingEngine.scene.geometryList[i].isInside(x, y)
        }
    }
    public toJson() {
        const data = [];
        for(let i = 0 ; i < this.drawingEngine.scene.geometryList.length; ++i) {
            data.push(this.drawingEngine.scene.geometryList[i].toJson());
        }
        return data;
    }
}