import { GeometryBase } from '../Base/GeometryBase';
import { ACTIVE_UI, NVector3, distanceToXY } from '../../lib';

import { Scene } from '../../Scene';

import { Collision } from '../Base/Collision';

export interface IPoint {
    x: number,
    y: number,
    z: number,
}

export class CPoint extends GeometryBase {
    
    r: number = 5;
    id: string;
    t: number = 0.0;
    color = '#00ff00';
    colorHover = '#0ffff0';

    collision: Collision;

    constructor(scene: Scene, param: IPoint) {
        super(scene, param.x, param.y);
        this.type = ACTIVE_UI.point;
        this.v = new NVector3(param.x, param.y, param.z);
    }
    public translate(x: number, y: number, long: number, lat: number) {
        this.v.x = x;
        this.v.y = y;
        this.v.long = long;
        this.v.lat = lat;
    }
    public isInside(x: number, y: number): boolean {
        if (distanceToXY(this.v.x, this.v.y, x, y) < this.r + 3) {
            this.isHover = true;
            return true;
        }
        this.isHover = false;
        return false;
    }
    public update(isPanning: boolean, toScreen: ( x: number, y: number) => number[] ) {
        if (isPanning && this.scene.drawingEngine.projection.isCustom) {
            const sv = toScreen(this.v.long, this.v.lat);
            this.v.x = sv[0];
            this.v.y = sv[1];
        }
    }
    public toJson() {
        return { x: this.v.x, y: this.v.y }
    }
    public renderPost(ctx: CanvasRenderingContext2D, isPanning: boolean) {

        let r = this.r + (Math.sin(this.t))
        if(this.isHover) {
            r += 3;
            ctx.fillStyle = this.colorHover;
        } else {
            ctx.fillStyle = this.color;
        }

        ctx.beginPath();
        ctx.arc(this.v.x, this.v.y, r  , 0.0, Math.PI * 2.0 );
        ctx.closePath();
        ctx.fill();
        if(this.isSelected) {
            ctx.lineWidth =3;
            ctx.strokeStyle = '#ffffff';
            ctx.stroke();
        }
        
        this.t += 0.06;
    }

}