
import { NWindow } from '../NWindow';
export class Builder {
    window: NWindow;

    divMain: HTMLDivElement;

    
    divMainBody: HTMLElement;
    divTopArea: HTMLElement;
    divFulldownArea: HTMLElement;
    divLeftUI: HTMLElement;
    divBottomArea: HTMLElement;

    divForGraphicContext: HTMLElement;

    private colorTopMenu = '#dfdfdf';
    private colorBGmenu = '#f3f3f3';

    constructor(window: NWindow){
        this.window = window;
    }

    public initPanel(titleString: string) {
        
        this.initDivMain();
        this.initDivTopArea();
        this.initDivFulldownArea();

        const box = document.createElement('div');
        box.style.float = 'left';
        box.style.height = '100%';
        box.style.padding = '10px';
        box.style.background = '#ff0000';
        this.divTopArea.appendChild(box);

        const title = document.createElement('div');
        title.style.float = 'left';
        title.style.padding = '10px';
        title.textContent = titleString;
        this.divTopArea.appendChild(title);

        this.initDivContextArea();
        this.initDivLeftArea();

        this.divForGraphicContext = document.createElement('div');
        this.divForGraphicContext.style.height = '100%';
        this.divForGraphicContext.id = 'divForGraphicContext';
        this.divMainBody.appendChild( this.divForGraphicContext);

        
        const closeBtn = document.createElement('button');
        closeBtn.style.top = '0px';
        closeBtn.style.left = '0px';
        closeBtn.style.height = '20px';
        closeBtn.style.width = '20px';
        closeBtn.style.zIndex = '13';
        closeBtn.style.float = 'right';
        // closeBtn.style.position = 'absolute';
        closeBtn.style.padding = '0px';
        closeBtn.style.zIndex = '20';
        // closeBtn.style.background = '#ff0000';
        // closeBtn.style.color = '#ffffff';
        closeBtn.textContent = 'X'
        closeBtn.onclick = () => {
            this.window.remove();
        };
        this.divTopArea.appendChild(closeBtn);

        this.initBottomArea(this.divMain);



        const bottomToolbarArea = document.createElement('div');
        // bottomToolbarArea.style.background = '#ff0000';
        bottomToolbarArea.style.height = '100%';
        bottomToolbarArea.style.width = '100px';
        bottomToolbarArea.style.float = 'left';
        this.divBottomArea.appendChild(bottomToolbarArea);


        const bottomTextarea = document.createElement('div');
        // bottomTextarea.style.background = '#0ff0f0';
        bottomTextarea.style.height = '100%';
        bottomTextarea.style.width = '80%';
        bottomTextarea.style.float = 'left';
        this.divBottomArea.appendChild(bottomTextarea);


    }
    public initDivTopArea(){
        let top = document.createElement('div');
        top.style.width = '100%';
        top.style.height = '40px';
        top.style.background = this.colorTopMenu; // 
        // top.style.position = 'relative';
        top.style.zIndex = '11';
        // top.style.padding = '5px';
        // top.textContent = 'NNA V3 controller'

        let isPressed = false;
        let prePos: number[] = [0 , 0];

        top.ondrag = (e: MouseEvent) => {
            // console.log('reall?', e);
        }
        top.onmouseenter = (e: MouseEvent) => {
            // console.log('onmouseenter?', e);
        }
        top.ondrag = (e: MouseEvent) => {
            // console.log('ondrag?', e);
        }
        top.onmousemove = (e: MouseEvent) => {
            if (isPressed) {
                const xOff = e.clientX - prePos[0];
                const yOff = e.clientY - prePos[1];
                // console.log(xOff, yOff);
                // console.log(+hostDiv.style.top);
                // console.log(hostDiv.clientLeft, parseFloat(hostDiv.style.left));
                this.divMain.style.top = parseFloat(this.divMain.style.top) + yOff +'px';
                this.divMain.style.left = parseFloat(this.divMain.style.left) + xOff +'px';
                // console.log('drgggg?', e);

                prePos[0] = e.clientX;
                prePos[1] = e.clientY;
            }
        }
        top.onmousedown = (e: MouseEvent) => {
            // console.log('down', e);
            prePos[0] = e.clientX;
            prePos[1] = e.clientY;
            isPressed = true;
            this.window.updateZindex();
        }
        top.onmouseup = (e: MouseEvent) => {
            // console.log('up', e);
            isPressed = false;
        }
        top.onmouseout = (e: MouseEvent) => {
            if (isPressed) {
                // console.log('onmouseout', e);
                const xOff = e.clientX - prePos[0];
                const yOff = e.clientY - prePos[1];
                // console.log(xOff, yOff);
                // console.log(+hostDiv.style.top);
                // console.log(hostDiv.clientLeft, parseFloat(hostDiv.style.left));
                this.divMain.style.top = parseFloat(this.divMain.style.top) + yOff +'px';
                this.divMain.style.left = parseFloat(this.divMain.style.left) + xOff +'px';
                // console.log('drgggg?', e);

                prePos[0] = e.clientX;
                prePos[1] = e.clientY;
            }
        }
        this.divTopArea = top;
        this.divMain.appendChild(top);
    }
    public appendCloseTop() {

    }

    public initBottomArea(hostDiv: HTMLElement) {

        let bootom = document.createElement('div');
        bootom.style.top = parseFloat(hostDiv.style.height) - parseFloat(hostDiv.style.top) - 10 + 'px';
        // top.style.left = parseFloat(hostDiv.style.width) - parseFloat(hostDiv.style.left) - 20 + 'px';
        bootom.style.width = '100%';
        bootom.style.height = '40px';
        bootom.style.background = this.colorBGmenu;
        // top.style.position = 'relative';
        bootom.style.zIndex = '11';
        // top.style.padding = '5px';
        // top.textContent = 'L'

        let isPressed = false;
        let prePos: number[] = [0 , 0];

        bootom.ondrag = (e: MouseEvent) => {
            // console.log('reall?', e);
        }
        bootom.onmouseenter = (e: MouseEvent) => {
            // console.log('onmouseenter?', e);
        }
        bootom.ondrag = (e: MouseEvent) => {
            // console.log('ondrag?', e);
        }
        bootom.onmousemove = (e: MouseEvent) => {
            if (isPressed) {
                const xOff = e.clientX - prePos[0];
                const yOff = e.clientY - prePos[1];

                hostDiv.style.width = parseFloat(hostDiv.style.width) + xOff + 'px';
                hostDiv.style.height = parseFloat(hostDiv.style.height) + yOff + 'px';

                // console.log('drgggg?', e, hostDiv.style.height);

                bootom.style.top = parseFloat(bootom.style.top) + yOff + 'px';
                bootom.style.left = parseFloat(bootom.style.left) + xOff + 'px';

                prePos[0] = e.clientX;
                prePos[1] = e.clientY;
            }
        }
        bootom.onmousedown = (e: MouseEvent) => {
            // console.log('down', e);
            prePos[0] = e.clientX;
            prePos[1] = e.clientY;
            isPressed = true;
        }
        bootom.onmouseup = (e: MouseEvent) => {
            // console.log('up', e);
            isPressed = false;
        }
        bootom.onmouseout = (e: MouseEvent) => {

            // @ts-ignore
            this.window.updateResize = setTimeout(() => {
                if (isPressed) {
                    // console.log('onmouseout', e);
                    const xOff = e.clientX - prePos[0];
                    const yOff = e.clientY - prePos[1];
                    hostDiv.style.width = parseFloat(hostDiv.style.width) + xOff + 'px';
                    hostDiv.style.height = parseFloat(hostDiv.style.height) + yOff + 'px';
                    // console.log(hostDiv.style.width)
    
                    bootom.style.top = parseFloat(bootom.style.top) + yOff + 'px';
                    bootom.style.left = parseFloat(bootom.style.left) + xOff + 'px';
    
                    prePos[0] = e.clientX;
                    prePos[1] = e.clientY;
                }
            }, 300);

            if (isPressed) {
                // console.log('onmouseout', e);
                const xOff = e.clientX - prePos[0];
                const yOff = e.clientY - prePos[1];
                hostDiv.style.width = parseFloat(hostDiv.style.width) + xOff + 'px';
                hostDiv.style.height = parseFloat(hostDiv.style.height) + yOff + 'px';
                // console.log(hostDiv.style.width)

                bootom.style.top = parseFloat(bootom.style.top) + yOff + 'px';
                bootom.style.left = parseFloat(bootom.style.left) + xOff + 'px';

                prePos[0] = e.clientX;
                prePos[1] = e.clientY;
            }

        }
        this.divBottomArea = bootom;
        hostDiv.appendChild(bootom);


    }
    public initDivMain(){
        this.divMain = document.createElement('div');
        this.divMain.style.width = `${this.window.width}px`;
        this.divMain.style.height = `${this.window.height}px`;
        // this.divMain.style.background = '#000000';
        this.divMain.style.position = 'absolute';
        this.divMain.style.zIndex = '10';
        this.divMain.id = 'NWindow';

        this.divMain.style.top = '100px'; //this.HTMLView.app.definition.app.divHost.style.top + 'px';
        this.divMain.style.left = '200px'; // = this.HTMLView.app.definition.app.divHost.clientWidth + 'px';

        this.window.divHost.appendChild(this.divMain);
        
    }
    public initDivFulldownArea(){
        this.divFulldownArea = document.createElement('div');
        this.divFulldownArea.style.height = '50px';
        this.divFulldownArea.style.width = '100%';
        this.divFulldownArea.style.background = this.colorBGmenu;
        // divUI.style.position = 'absolute';

        this.divMain.appendChild(this.divFulldownArea);
    }
    public initDivContextArea(){
        this.divMainBody = document.createElement('div');
        this.divMainBody.style.height = '100%';
        this.divMainBody.style.width = '100%';
        // this.divMainBody.style.background = '#ff0000'; // this.colorBGmenu;
        // this.divMainBody.style.position = 'absolute';
        // this.divMainBody.style.position = 'relative';
        this.divMainBody.id = 'divContext';
        this.divMainBody = this.divMainBody;
        this.divMain.appendChild(this.divMainBody);
    }
    public initDivLeftArea() {
        this.divLeftUI = document.createElement('div');
        this.divLeftUI.style.height = '100%';
        this.divLeftUI.style.width = '100px';
        this.divLeftUI.style.background = this.colorBGmenu;
        this.divLeftUI.style.float = 'left';

        this.divMainBody.appendChild( this.divLeftUI);
    }
}
