import { NVector3, getRandomArbitrary } from '../../../lib';
import { BoidsPath } from './BoidsPath';
import { Boid } from './Boid';

import { Node } from '../../GraphSystem/GraphCore/Graph/Node';

// import { AppDrawing } from '../../AppDrawing/AppDrawing';

// http://www.red3d.com/cwr/steer/

// https://Flockdevelopment.tutsplus.com/tutorials/understanding-steering-behaviors-path-following--Flockdev-8769
// https://Flockdevelopment.tutsplus.com/tutorials/understanding-steering-behaviors-leader-following--Flockdev-10810
// https://Flockdevelopment.tutsplus.com/tutorials/understanding-steering-behaviors-queue--Flockdev-14365

// https://gamedevelopment.tutsplus.com/series/understanding-steering-behaviors--gamedev-12732
export class BoidsBehaviors {
    public static mouse: NVector3 = new NVector3(100, 100, 0);
    public static width: number = 400;
    public static height: number = 400;
    public static showForces: boolean = false;
    public static instance: BoidsBehaviors;
    public boids: Boid[] = [];
    public boidsPath: BoidsPath;
    // public forces: Sprite;
    public t: number;
    isOriented: boolean;
    activeBase64ID: string;
    constructor(width: number, height: number) {
        BoidsBehaviors.instance = this;
        BoidsBehaviors.width = width;
        BoidsBehaviors.height = height;
        this.isOriented = false;
        this.t = 0.0;
        // this.initPath(new BoidsPath(20));
        // addEventListener(Event.ADDED_TO_STAGE, init);
    }
    public InitPathByAPolyline(boidsPath: BoidsPath, numBoid: number = 7) {
        this.boidsPath = boidsPath;
        this.InitBoid(numBoid);
    }
    public InitPathByNode(boidsPath: Node[], numBoid: number = 7) {
    //     console.log(boidsPath)
        this.boidsPath = new BoidsPath(10);

        for(let i = 0 ; i < boidsPath.length; ++i) {
            if(boidsPath[i]){
                this.boidsPath.AddNode(boidsPath[i].point);
            }
        }
        this.InitBoid(numBoid);
    }
    public InitPath(boidsPath: BoidsPath, numPath: number = 8, numBoid: number = 7) {
        this.boidsPath = boidsPath;
        this.Init(numPath, numBoid);
    }
    public Init(numPath: number, numBoid: number): void {
        this.reset();
        this.boidsPath.r = 30;

        // Add path nodescircles
        let maxPathNodes: number = numPath;
        for (let i = 0; i < maxPathNodes; i++) {
            let node: NVector3 = new NVector3(BoidsBehaviors.width * i / maxPathNodes + 50, BoidsBehaviors.height * Math.random() * 0.8 + 20, 0);
            // let node: NVector3 = new NVector3(this.boidsPath.nodes[0].x, this.boidsPath.nodes[0].y);
            this.boidsPath.AddNode(node);
        }
        this.InitBoid(numBoid);
    }
    public InitBoid(numBoid: number = 7) {
        let boid: Boid;
        for (let i = 0; i < numBoid; i++) {
            boid = new Boid(BoidsBehaviors.width / 2 + Math.random() * 50, 20 * Math.random(), 20 + Math.random() * 20);
            boid.activeBase64ID = this.activeBase64ID;
            boid.boidsPath = this.boidsPath;
            this.boids.push(boid);
        }

        // this.forces = new Sprite();

        // addChild(forces);

        // addChild(new Button(Boids.width - 80, Boids.height - 60, "Reload", Boids.instance.init));
        // addChild(new Button(Boids.width - 130, Boids.height - 60, " + ", IncreasePathRadius));
        // addChild(new Button(Boids.width - 180, Boids.height - 60, " - ", DecreasePathRadius));
        // addChild(new Button(Boids.width - 260, Boids.height - 60, "Forces", ToggleShowForces));
    }
    public ShiftSpeed(min: number, max: number) {
        for (let b of this.boids) {
            b.maxVelocity = getRandomArbitrary(min, max);
        }
    }
    public IncreasePathRadius(): void {
        BoidsBehaviors.showForces = true;
        this.boidsPath.r += 2;
    }
    public DecreasePathRadius(): void {
        BoidsBehaviors.showForces = true;
        this.boidsPath.r -= 2;
    }
    public ToggleShowForces(): void {
        BoidsBehaviors.showForces = !BoidsBehaviors.showForces;
    }
    public ShiftRadius(v: number) {
        this.boidsPath.r = v;
    }
    public ShiftForce(min: number, max: number) {
        for (let b of this.boids) {
            b.maxVelocity = getRandomArbitrary(min, max);
        }
    }
    public ShiftOriented(v: boolean) {
        for (let b of this.boids) {
            b.isOriented = v;
        }
    }
    public reset(): void {
        // while (numChildren) {
        //     removeChildAt(0);
        // }
        // boids.length = 0;
        // path.getNodes().length = 0;
        // showForces = false;

        // stage.removeEventListener(MouseEvent.CLICK, ToggleShowForces);
    }
    public Update(): void {
        for (let i = 0; i < this.boids.length; i++) { this.boids[i].Update(); }
        if (this.t > 16) { this.t = 0; }
        this.t += 0.5;  // AppDrawing.app.cCanvas.delta;
    }
    public Render(ctx: CanvasRenderingContext2D) {
        ctx.globalCompositeOperation = 'lighter';
        this.drawPath(ctx);
        for (let i = 0; i < this.boids.length; i++) {
            this.boids[i].Render(ctx);
            // this.drawForces(ctx, this.boids[i]);
        }
        ctx.globalCompositeOperation = 'source-over';
    }
    public drawPath(ctx: CanvasRenderingContext2D): void {
        let nodes: NVector3[] = this.boidsPath.GetNodes();
        for (let i: number = 0; i < nodes.length; ++i) {
            this.drawPathPoint(ctx, nodes[i], '#FF00CC');

            if (BoidsBehaviors.showForces) {
                this.drawCircle(ctx, nodes[i], 'gray', this.boidsPath.r);
            }
        }

        ctx.beginPath();
        ctx.lineWidth = 0.2;
        ctx.strokeStyle = '#ffffff';
        ctx.setLineDash([4, 4]);
        ctx.lineDashOffset = -this.t;
        ctx.moveTo(nodes[0].x, nodes[0].y);
        for (let i = 1; i < nodes.length; ++i) { ctx.lineTo(nodes[i].x, nodes[i].y); }
        // ctx.closePath();
        ctx.stroke();
        ctx.setLineDash([0, 0]);

        ctx.beginPath();
        ctx.lineWidth = 0.3;
        ctx.strokeStyle = '#000000';
        ctx.setLineDash([4, 8]);
        ctx.lineDashOffset = -this.t;
        ctx.moveTo(nodes[0].x, nodes[0].y);
        for (let i = 1; i < nodes.length; ++i) { ctx.lineTo(nodes[i].x, nodes[i].y); }
        // ctx.closePath();
        ctx.stroke();
        ctx.setLineDash([0, 0]);
    }
    public drawPathPoint(ctx: CanvasRenderingContext2D, force: NVector3, color: string): void {

        ctx.fillStyle = 'rgba(255,0,0,0.6)';
        ctx.beginPath();
        ctx.arc(force.x, force.y, 2.5, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
        ctx.stroke();
        // forces.graphics.lineStyle(1, 0x323232);
        // forces.graphics.beginFill(0xFF5000);
        // forces.graphics.drawCircle(force.x, force.y, 6);
        // forces.graphics.endFill();
    }
    public drawCircle(ctx: CanvasRenderingContext2D, force: NVector3, color: string, radius: number): void {
        ctx.fillStyle = color;
        ctx.beginPath();
        ctx.arc(force.x, force.y, 2, 0, 3.14 * 2);
        ctx.closePath();
        ctx.fill();
        ctx.stroke();

        // forces.graphics.lineStyle(1, 0x323232);
        // //forces.graphics.beginFill(0xFF5000);
        // forces.graphics.drawCircle(force.x, force.y, radius);
        // //forces.graphics.endFill();
    }
    public set IsOrient(v: boolean) {
        this.isOriented = v;
        this.ShiftOriented(v);
    }
    public get IsOrient() {
        return this.isOriented;
    }
}