import { GraphSystem } from '..';
import { NVector3, NLine } from '../../../lib'
import { Graph } from './Graph';
import { Tree } from './Tree/Tree';

import { costFnDistanceGeo } from '../GraphUtility/GraphUtility';

export class GraphCore {


    public gs: GraphSystem;
    public trees: Tree[] = [];
   // public List<int> idOrigin;
    public destinations: NVector3[] = [];
    public idDestination: number[] = [];
    public graph: Graph;
    constructor(gs: GraphSystem) {
        this.gs = gs;
        this.trees = [];
        this.destinations = [];
        this.idDestination = [];
    }
    public initGraph(lns: NLine[]) {
        this.graph = new Graph(this);
        this.graph.initGraph(lns);
    }
    public setUnit(unit: string) {
        
    }
    public computePath(origins: NVector3[], destinations: NVector3[], BETA: number = 0.002) {
        this.trees = [];
        let oWeight: number[] = [];
        let dWeight: number[] = [];
        this.computePathWeighted(origins, oWeight, destinations, dWeight, BETA);
    }
    public computePathWeighted(origins: NVector3[], oWeight: number[], destinations: NVector3[], dWeight: number[], BETA: number = 0.002) {
        while (origins.length < oWeight.length) { oWeight.push(1); }
        while (destinations.length < dWeight.length) { dWeight.push(1); }

        this.updatePropagation(origins, oWeight);
        this.updateBackpropagation(destinations, dWeight);
        this.mergeAgents();
    }
    public updatePropagation(origins: NVector3[], oWeight: number[]) {

        for (let i = 0; i < origins.length; ++i) {
            let p = origins[i];

            if (i == this.trees.length -1) {
                if (this.trees[i].pointOrigin.distanceToGeo(p) < 0.1) {
                    this.trees[i].updatePropagation(costFnDistanceGeo, 0.002);
                }
                else {
                    this.trees[i].updateOriginByPt(p);
                    this.trees[i].updatePropagation(costFnDistanceGeo, 0.002);
                }
            }
            else {
                let tree: Tree = new Tree(this, p);
                tree.updatePropagation(costFnDistanceGeo, 0.002);
                this.trees.push(tree);
            }


        }
    }
    public updateBackpropagation(destination: NVector3[], dWeight: number[], BETA: number = 0.002) {

        for (let i = 0; i < destination.length; ++i) {
            if (i == this.destinations.length - 1) {
                if (destination[i].distanceToGeo(this.destinations[i]) < 0.1) {
                    this.idDestination.push(this.idDestination[i]);
                }
                else {
                    let n = this.graph.FindClosestNode(destination[i]);
                    if (n){
                        this.idDestination[i] = n.id;
                        this.destinations[i] = destination[i];
                    }
 
                }
            }
            else {
                let n = this.graph.FindClosestNode(destination[i]);
                if (n){
                    this.idDestination.push(n.id);
                    this.destinations.push(destination[i]);
                }
            }
        }

        for (let i = 0; i < this.trees.length; ++i) {
            let tree = this.trees[i];
            tree.updateBackpropagation(this.idDestination);
        }
    }
    public mergeAgents() {
        this.graph.updatePropagation(this.trees, costFnDistanceGeo, 0.002);

        for (let i = 0; i < this.trees.length; ++i) {
            for (let j = 0; j < this.trees[i].nodes.length; ++j) {
                this.graph.nodes[j].countVisit += this.trees[i].nodes[j].countVisit;
                this.graph.nodes[j].countOrigin += this.trees[i].nodes[j].countOrigin;
                this.graph.nodes[j].countDestination += this.trees[i].nodes[j].countDestination;
            }
        }

    }
}
