import { GraphCore } from '..';
import { Node, Edge } from '../Graph';
import { NVector3, NPolyline } from '../../../../lib';
export class Tree {
    public gc: GraphCore;

    public Optimizer: (current: Node, np: Node, Beta: number) => number;
    // public transitionCostDelegate Optimizer;
    // public delegate double transitionCostDelegate(Node n0, Node n1, double Beta);

    public edges: Edge[] = [];
    public nodes: Node[] = [];

    public nodeDestinations: Node[] = [];
    public nodeOrigin: Node;
    public pointOrigin: NVector3;

    public polylines: NPolyline[] = [];

    public paths: Node[][] = [];

    constructor(gc: GraphCore, p: NVector3) {
        this.gc = gc;
        this.duplicateGraph();
        this.updateOriginByPt(p);
    }
    public updateOriginByPt(p: NVector3) {
        let n = this.gc.graph.FindClosestNode(p);
        if(n){
            this.nodeOrigin = this.nodes[n.id];
            this.pointOrigin = new NVector3(p.x, p.y, p.z);
        }

    }
    public updatePropagation(Optimizer: (current: Node, np: Node, Beta: number) => number, Beta: number) { //, PixelMap pMap, transitionCostDelegate cost) {
        this.Optimizer = Optimizer;
        
        for (let i = 0; i < this.nodes.length; ++i) {
            this.nodes[i].networkDistance = -1;
        }

        let queue: Node[] = [];
        this.nodeOrigin.networkDistance = 0;
        queue.push(this.nodeOrigin);
        for (let i = 0; i < 1000000; ++i) {
            if (queue.length == 0) { break; }
            let current: Node = queue[0];
            current.euclideanDistance = current.point.distanceTo(this.nodeOrigin.point);
            queue.shift();
            for (let j = 0; j < current.nodes.length; ++j) {
                let np: Node = current.nodes[j];
                let newcost = Optimizer(current, np, Beta);
                let result = (current.networkDistance + newcost);
                let newtemp = result; /// this.UNIT_VALUE; // result < 0.001 ? result  : result / this.UNIT_VALUE;
                if (np.networkDistance == -1.0 || newtemp < np.networkDistance) {
                    np.networkDistance = newtemp;
                    queue.push(np);
                }
            }
        }
    }
    public updateBackpropagation(desID: number[]): Node[][] {
        this.nodeDestinations = new Node[desID.length];

        let paths: Node[][] = [];
        let i = 0; let j = 0; let k = 0;
        for (i = 0; i < desID.length; ++i) {
            let destination: Node = this.nodes[desID[i]];
            this.nodeDestinations[i] = destination;
            let path: Node[] = [];
            if (destination.networkDistance != -1.0) {
                path.push(this.nodes[destination.id]);
                let current: Node = destination;
                for (j = 0; j < 100000; ++j) {
                    let smallest: Node = current;
                    smallest.countVisit++;
                    for (k = 0; k < current.nodes.length; ++k) {
                        let np: Node = current.nodes[k];
                        if (np.networkDistance < smallest.networkDistance) {
                            smallest = np;
                        }
                    }
                    if (smallest == current) {
                        path.push(this.nodeOrigin); // start node
                        path[0].euclideanDistance = path[0].point.distanceTo(this.nodeOrigin.point);
                        paths.push(path);

                        break;
                    }
                    current = smallest;
                    path.push(this.nodes[current.id]);

                    // if (current == this.destination) { break; }
                }
            }

        }
        this.paths = paths;
        return paths;
    }
    public GetPathsPolylines(min: number, max: number): NPolyline[] {
        let pls: NPolyline[] = [];
        for (let i = 0; i < this.paths.length; ++i) {
            let pl: NPolyline = new NPolyline();
            for (let j = 0; j < this.paths[i].length; ++j) {
                pl.push(this.paths[i][j].point);
            }
            if (min < this.paths[i][0].networkDistance && this.paths[i][0].networkDistance < max) {
                pls.push(pl);
            }
        }
        this.polylines = pls;
        return this.polylines;
    }

    public duplicateGraph(): void {
        let i = 0;
        let id0 = -1;
        let id1 = -1;

        this.nodes = []; // new Node[this.gc.graph.nodes.Count];
        this.edges = []; // new Edge[this.gc.graph.edges.Count];

        for (i = 0; i < this.gc.graph.nodes.length; ++i) {
            this.nodes.push(this.gc.graph.nodes[i].deepCopy());
        }
        for (i = 0; i < this.gc.graph.edges.length; ++i) {
            id0 = this.gc.graph.edges[i].n0.id;
            id1 = this.gc.graph.edges[i].n1.id;
            let e: Edge = new Edge(this.nodes[id0], this.nodes[id1]);
            e.id = this.gc.graph.edges[i].id;
            this.edges.push(e); ;
        }
    }
}