import { APP } from '../../lib';
import { HTMLView } from '../HTMLView';
export class GraphView {
    HTMLView: HTMLView;
    div: HTMLElement;
    constructor(HTMLView: HTMLView) {
        this.HTMLView = HTMLView;

        this.initPanel();
    }
    public remove(){
        this.HTMLView.networkUI = undefined;
        // this.HTMLView.app.definition.app.divHost.removeChild(this.div);
    }
    public initPanel(){

        let div = document.createElement('div');
        div.style.width = '200px';
        div.style.height = '400px';
        div.style.background = '#ffffff';
        div.style.position = 'absolute';
        div.style.zIndex = '10';

        div.style.top = '100px'; //this.HTMLView.app.definition.app.divHost.style.top + 'px';
        // div.style.left = this.HTMLView.app.definition.app.divHost.clientWidth + 'px';


        let top = document.createElement('div');
        top.style.width = '100%';
        top.style.height = '60px';
        top.style.background = '#efefef';
        top.style.position = 'relative';
        top.style.zIndex = '11';
        // top.style.padding = '5px';
        top.textContent = 'NNA V3 controller'

        let isPressed = false;
        let prePos: number[] = [0 , 0];

        top.ondrag = (e: MouseEvent) => {
        //     console.log('reall?', e);
        }
        top.onmouseenter = (e: MouseEvent) => {
        //     console.log('onmouseenter?', e);
        }
        top.ondrag = (e: MouseEvent) => {
        //     console.log('ondrag?', e);
        }
        top.onmousemove = (e: MouseEvent) => {
            if (isPressed) {
                const xOff = e.clientX - prePos[0];
                const yOff = e.clientY - prePos[1];
            //     console.log(xOff, yOff);
            //     console.log(+div.style.top);
            //     console.log(div.clientLeft, parseFloat(div.style.left));
                div.style.top = parseFloat(div.style.top) + yOff +'px';
                div.style.left = parseFloat(div.style.left) + xOff +'px';
            //     console.log('drgggg?', e);

                prePos[0] = e.clientX;
                prePos[1] = e.clientY;
            }
        }
        top.onmousedown = (e: MouseEvent) => {
        //     console.log('down', e);
            prePos[0] = e.clientX;
            prePos[1] = e.clientY;
            isPressed = true;
        }
        top.onmouseup = (e: MouseEvent) => {
        //     console.log('up', e);
            isPressed = false;
        }
        top.onmouseout = (e: MouseEvent) => {
            if (isPressed) {
            //     console.log('onmouseout', e);
                const xOff = e.clientX - prePos[0];
                const yOff = e.clientY - prePos[1];
            //     console.log(xOff, yOff);
            //     console.log(+div.style.top);
            //     console.log(div.clientLeft, parseFloat(div.style.left));
                div.style.top = parseFloat(div.style.top) + yOff +'px';
                div.style.left = parseFloat(div.style.left) + xOff +'px';
            //     console.log('drgggg?', e);

                prePos[0] = e.clientX;
                prePos[1] = e.clientY;
            }
        }
        // top.style.top = '100px';
        // top.style.left = '100px';

        div.appendChild(top);
        
        let btn = document.createElement('button');
        btn.style.height = '70px';
        btn.style.width = '60px';
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.textContent = 'Import'
        btn.onclick = () => {
            // this.HTMLView.app.importDummyData();
        };
        div.appendChild(btn);

        btn = document.createElement('button');
        btn.style.height = '70px';
        btn.style.width = '60px';
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.textContent = 'Origin'
        btn.onclick = () => {
            this.HTMLView.app.apiCall(APP.NETWORK, 'addOrigin' , undefined);
        };
        div.appendChild(btn);


        btn = document.createElement('button');
        btn.style.height = '70px';
        btn.style.width = '60px';
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.textContent = 'Destin'
        btn.onclick = () => {
        //     console.log('nna');
        };
        div.appendChild(btn);

        btn = document.createElement('button');
        btn.style.height = '70px';
        btn.style.width = '60px';
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.textContent = 'Destin'
        btn.onclick = () => {
        //     console.log('nna');
        };
        div.appendChild(btn);

        btn = document.createElement('button');
        btn.style.height = '70px';
        btn.style.width = '60px';
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.textContent = 'Destin'
        btn.onclick = () => {
        //     console.log('nna');
        };
        div.appendChild(btn);

        // this.HTMLView.app.definition.app.divHost.appendChild(div);

        
        btn = document.createElement('button');
        btn.style.top = '0px';
        btn.style.left = '180px';
        btn.style.height = '20px';
        btn.style.width = '20px';
        btn.style.zIndex = '13';
        btn.style.position = 'absolute';
        btn.style.padding = '0px';
        btn.style.zIndex = '20';
        // btn.style.background = '#ff0000';
        // btn.style.color = '#ffffff';
        btn.textContent = 'X'
        btn.onclick = () => {
            this.remove();
        };
        div.appendChild(btn);

        this.div = div;
    }
}