import { shuffle, NVector3 } from '../../../../../lib';
export class Node{
    vec: NVector3;
    id: number = -1;
    constructor(x: number, y: number, long: number, lat: number){
        this.vec = new NVector3(x, y, 0);
        this.vec.long = long;
        this.vec.lat = lat;
    }
    public distanceTo(node: Node){
        return this.vec.distanceTo(node.vec);
    }
}
export class Path{
    pathsOrigin: Node[] = [];
    nodes: (Node | undefined)[] = [];
    fitness = 0.0;
    distance = 0;
    constructor(pathsOrigin: Node[] ){
        this.pathsOrigin = pathsOrigin;
        this.nodes = [];
        this.fitness = 0.0;
        this.distance = 0;

        for(let i = 0 ; i < this.pathsOrigin.length; ++i){
            this.nodes.push(undefined);
        }
    }
    public generateIndividual(){
        for(let cityIndex = 0; cityIndex < this.pathsOrigin.length; ++cityIndex){
            this.setCityInit(cityIndex, this.pathsOrigin[cityIndex]);
        }
        shuffle(this.nodes);
    }
    public setCityInit(i: number, city: Node){
        this.nodes[i] = city;
        this.fitness = 0.0;
        this.distance = 0;
    }
    public getFitness(){
        if (this.fitness === 0){
            this.fitness = 1.0 / (this.getDistance());
        }
        return this.fitness;
    }
    public getDistance(){
        if (this.distance === 0){
            let tourDistance = 0;
            for (let i = 0 ; i < this.nodes.length; ++i ){
                let fromNode = this.nodes[i];

                if(fromNode){
                    let otherNode = undefined
                    if (i === this.nodes.length -1 ){
                        otherNode = this.nodes[0];
                    } else{
                        otherNode = this.nodes[i+1];
                    }

                    if(otherNode){
                        tourDistance += fromNode.distanceTo(otherNode);
                    }
                }
 
            }
            this.distance = tourDistance;
        }
        return this.distance;
    }
    public containsCity(city: Node): boolean{
        for(let i = 0 ; i < this.nodes.length; ++i) {
            if(this.nodes[i] === city) { return true }
        }
        return false;
    }
}
export class Population{
    paths: (Path | undefined) [] = [];
    constructor(nodes: Node[] , populationSize: number, initialise: boolean){
        this.paths = [];
        for(let i = 0; i <populationSize; ++i){
            this.paths.push(undefined);
        }
        if (initialise){
            for(let i = 0 ; i <populationSize; ++i){
                let newTour = new Path(nodes);
                newTour.generateIndividual();
                this.paths[i] = newTour;
            }
        }
    }
    public getFittestPath(){
        let fittest: undefined | Path = this.paths[0];
        for(let i = 0; i < this.paths.length; ++i){
            let other = this.paths[i];
            if (fittest && other){
                if (fittest.getFitness() <= other.getFitness()){
                    fittest = other;
                }
            }
        }
        return fittest;
    }
}