import { NWindow } from '../NWindow';
export class Controller {
    window: NWindow;
    constructor(window: NWindow){
        this.window = window;
    }
    public addIconTopPanel(param: {text: string, callBack: () => void, width: number }){
        let btn = document.createElement('button');
        // btn.style.height = '70px';
        btn.style.width = `${param.width}px`;
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.style.border = 'none';
        btn.textContent = param.text;
        btn.onclick = () => param.callBack();
        this.window.builder.divFulldownArea.appendChild(btn);
    }
    public addToggleTopPanel(param: {text: string, callBack: () => void, width: number }){
        let btn = document.createElement('button');
        // btn.style.height = '70px';
        btn.style.width = `${param.width}px`;
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.style.border = 'none';
        btn.textContent = param.text;
        btn.onclick = () => param.callBack();
        this.window.builder.divFulldownArea.appendChild(btn);
    }
    public addIconLeftPanel(param: {text: string, callBack: () => void }){
        let btn = document.createElement('button');
        btn.style.height = '50px';
        btn.style.width = '50px';
        btn.style.zIndex = '13;'
        btn.style.position = 'relative';
        btn.style.border = 'none';
        btn.style.color = '#000000';
        btn.style.background = '#fafafa';
        btn.textContent = param.text;
        btn.onclick = () => param.callBack();
        btn.onmouseenter = () => {
            btn.style.color = '#ffffff';
            btn.style.background = '#888888';
        }
        btn.onmouseleave = () => {
            btn.style.color = '#000000';
            btn.style.background = '#fafafa';
        }
        this.window.builder.divLeftUI.appendChild(btn);
    }
    public addIconLeft(){

    }
}