import { DrawingEngine } from '../';
import { ACTIVE_UI } from '../lib';
import { GeometryBase } from '../Geometry/Base/GeometryBase';
import { CPoint } from '../Geometry/Point';

export class Scene {
    public drawingEngine: DrawingEngine;
    
    public _capture: GeometryBase | undefined = undefined; 
    public geometryList : GeometryBase[] = [];
    public pointList : CPoint[] = [];

    public geoDict: {} = {};
    constructor(drawingEngine: DrawingEngine) {
        this.drawingEngine = drawingEngine;
    }
    public get capture(): GeometryBase | undefined {
        return this._capture;
    }
    public set capture(g: GeometryBase | undefined) {
        if (g) {
            this.drawingEngine.ableToPan = false;
        } else {
            this.drawingEngine.ableToPan = true;
        }
        // console.log(g);
        this._capture = g;
    }
    public addGeometry(g: GeometryBase) {
        this.geoDict[g.id] = g;
        this.geometryList.push(g);
        if(g.type === ACTIVE_UI.point) {
            this.pointList.push(g as CPoint);
        }
    }

}