import { Node } from '.'
import { NVector3, CONNECTIVITY } from '../../../../lib';
export class Edge {

    public id: number = -1;
    public n0: Node;
    public n1: Node;
    public mp: NVector3;
    public CONNECTIVITY: CONNECTIVITY;

    constructor(n0: Node, n1: Node){
        this.n0 = n0;
        this.n1 = n1;

        this.n0.edges.push(this);
        this.n1.edges.push(this);

        n0.nodes.push(n1);
        n1.nodes.push(n0);

        this.mp = this.GetMidPoint();
        this.CONNECTIVITY = CONNECTIVITY.CONNECTED;
    }
    public GetMidPoint(): NVector3 {
        return NVector3.getMidVec(this.n0.point, this.n1.point);
    }
    public updateScreenPos( toScreen: (x: number, y: number) => number[] ) {
        this.n0.updateScreenPos(toScreen);
        this.n1.updateScreenPos(toScreen);
    }
}