import { NVector3 } from './lib';
import { OPTIMIZER } from './lib/enum';
import { GridSystemScreen } from './Core/GridSystemScreen';
import { GridSystemDynamic } from './Core/GridSystemDynamic';

export { OPTIMIZER }
export class Clustering {
    optimizerType: OPTIMIZER;

    optimizer: GridSystemDynamic | GridSystemScreen;

    mv: NVector3 = new NVector3(0, 0);

    inspection: boolean = true;

    constructor(optimizer: OPTIMIZER) {
        this.optimizerType = optimizer;
        
        switch (optimizer) {
            case OPTIMIZER.GRID_SCREEN:
                this.optimizer = new GridSystemScreen();
                break;
            case OPTIMIZER.GRID:
                this.optimizer = new GridSystemDynamic();
                break;
            default:
                break;
        }

        console.debug('clustering... v 0.1.5')
    }
    public push(vs: NVector3[], width: number, height: number) {
        this.optimizer.points = vs;
        this.update(width, height);
    }
    public mouseInteraction(x: number, y: number){
        this.mv = this.optimizer.getCellAtPoint(new NVector3(x, y)).vec;
    }
    public update(width: number, height: number) {
        this.optimizer.update(width, height);

    }
    public render(ctx: CanvasRenderingContext2D, isStationary: boolean, width: number, height: number) {
        if (isStationary) {
            this.update(width, height);
        }
        this.optimizer.render(ctx)

        if(this.mv){
            ctx.fillStyle = '#00ff00';
            ctx.beginPath();
            ctx.arc(this.mv.x, this.mv.y,9, 0.0, 3.14 * 2)
            ctx.closePath();
            ctx.fill();
        }
        

    }

}