// import { MouseEventData } from '../../../../Core/EventInteraction';
import { DrawingEngine } from '../';
import { CommandBase } from './Commands/CommandBase';
import { CommandSelect, CommandPoint, CommandLine } from './Commands/CommandShape';

import { COMMAND } from '../lib'

import { fetchJson } from '../../../NJSCore/IO';
import { CPoint } from '../Geometry/Point';

export class ActiveCommandController {
    drawingEngine: DrawingEngine;

    
    activeCommand: CommandBase;
    public commands = {};

    hasShift: boolean = false;
    inspection: boolean = false;
    constructor(drawingEngine: DrawingEngine) {
        this.drawingEngine = drawingEngine;

        this.commands = {
            SELECT : new CommandSelect(this),
            POINT : new CommandPoint(this),
            LINE : new CommandLine(this),
        }

        // this.commands.Line = new CommandBase(this);
        // this.commands.Point = new CommandBase(this);
        // this.commands.Line = new CommandBase(this);

        this.activeCommand = this.commands[COMMAND.SELECT];
    }
    public setCommander(commander: string) {
        switch (commander) {
            case 'save':
            //     console.log('save!!');
                break;
            case 'load':
            //     console.log('load!!');
                break;
            default:
                break;
        }

        // console.log(commander);
        switch (commander as COMMAND) {
            case COMMAND.SELECT:
                // console.log(commander);
                this.activeCommand = this.commands[commander];
                // console.log(this.activeCommand);
                break;
            case COMMAND.POINT:
                // console.log(commander);
                this.activeCommand = this.commands[commander];
                // console.log(this.activeCommand);
                break;
            case COMMAND.LINE:
                // console.log(commander);
                this.activeCommand = this.commands[commander];
                // console.log(this.activeCommand);
                break;
            // case 'point':
            //     this.activeCommand = this.commands[commander];
            //     break;
            default:
                break;
        }
    }
    public apiCall(command: string): any {


        switch (command ) {
            case 'getSelectedCPoint':
                return this.drawingEngine.geometryCommon.getSelCPoint();
            case 'importZipData':
                return this.popZipData();
            case 'importClustering':
                return this.drawingEngine.addon.InitClustering();
            case 'importClusteringCollision':
                return this.drawingEngine.addon.InitClusteringCollision();

            case 'save':
                return this.drawingEngine.geometryCommon.toJson();

            default:
                break;
        }

    }

    public async popZipData (){
                
        const json = await fetchJson('https://raw.githubusercontent.com/NamjuLee/data/master/US/zip.json');
    //     console.log(json);
        let count = 0;
        for(let i of json['pos']) {
            // console.log(i);

            const p = this.greatePointByGeoPos(i[0], i[1]);
            p.t += Math.random() * 10;
            if(count++ > 2000) { return; }
        }
    

    }
    public greatePointByGeoPos(long: number, lat: number) {
        const spos = this.drawingEngine.projection.toScreen(long, lat);
        const p = new CPoint(this.drawingEngine.scene, {x: spos[0], y: spos[1], z: 0 });
        p.v.long = long;
        p.v.lat = lat;
        this.drawingEngine.scene.addGeometry(p);
        return p;
    }
}