import { NVector3 } from '../../../lib';
export class BoidsPath {
    r: number = 5;
    nodes: NVector3[];
    constructor(r: number) {
        this.nodes = [];
        this.r = r;
    }
    public AddNode(node: NVector3): void {
        this.nodes.push(node);
    }
    public GetNodes(): NVector3[] {
        let p: NVector3[] = [];
        for (let i = 0; i < this.nodes.length; ++i) {
            p.push(new NVector3(this.nodes[i].x, this.nodes[i].y, 0));
            // p.push(this.nodes[i]);
        }
        return p;
    }
}