import { Core } from '../Core';
import {GASystem } from './GASystem/GASystem';
export class AgencySystem {

    nnaEngine: Core

    gaSystem: GASystem;
    constructor(nnaEngine: Core) {
        this.nnaEngine = nnaEngine;

        this.gaSystem = new GASystem(this);
    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        this.gaSystem.render(ctx, isPanning);
    }
    public mouseClick(x: number, y: number){
        this.gaSystem.mouseClick(x, y);
    } 
    public update() {
        this.gaSystem.initByCPoint(undefined);
    }

}