// """
// This Python code is based on Java code by Lee Jacobson found in an article
// entitled "Applying a genetic algorithm to the travelling salesman problem"
// that can be found at: http://goo.gl/cJEY1
// https://gist.github.com/turbofart/3428880
// """
import { Population, Node, Path } from './Core';

export class GeneticAlgorithm {
    private nodes: Node[];
    private mutationRate: number;
    private nodeSize: number;
    private elitism: boolean;
    constructor (tourmanager: Node[], mutationRate=0.05, nodeSize=5, elitism=true){
        this.nodes = tourmanager
        this.mutationRate = mutationRate
        this.nodeSize = nodeSize
        this.elitism = elitism
    }
    public evolvePopulation(pop: Population){
        const newPopulation = new Population(this.nodes, pop.paths.length, false)
        let elitismOffset = 0
        if (this.elitism){
            const thePath = pop.getFittestPath();
            if (thePath){
                newPopulation.paths[0] = thePath;
                elitismOffset = 1
            }
        }
        for(let i = elitismOffset; i < newPopulation.paths.length; ++i){
            let parent1 = this.tournamentSelection(pop)
            let parent2 = this.tournamentSelection(pop);
            if (parent1 && parent2){
                let child = this.crossover(parent1, parent2);
                newPopulation.paths[i] = child;
            }
        }
        for(let i = elitismOffset; i < newPopulation.paths.length; ++i){
            let path = newPopulation.paths[i];
            if (path) {
                this.mutate(path);
            }
        }
        return newPopulation
    }
    private crossover(parent1: Path, parent2: Path){
        let child = new Path(this.nodes);
        
        let startPos = Math.floor(Math.random() * parent1.nodes.length);
        let endPos = Math.floor(Math.random() * parent1.nodes.length);
        
        for (let i = 0; i < child.nodes.length; ++i){
            const place = parent1.nodes[i];
            if (place){
                if (startPos < endPos && i > startPos && i < endPos) {
                    child.setCityInit(i, place);
                } else if(startPos > endPos){
                    if ((i < startPos && i > endPos) == false ){
                        child.setCityInit(i, place);
                    }
                }
            }
        }
        for (let i = 0; i < parent2.nodes.length; ++i){
            const place = parent2.nodes[i];
            if(place){
                if (child.containsCity(place) == false ){
                    for (let ii = 0; ii < child.nodes.length; ++ii){
                        if (child.nodes[ii] == undefined){
                            child.setCityInit(ii, place);
                            break;
                        }
                    }
                }
            }

        }
        return child;
    }
    private mutate(path: Path){
        for(let tourPos1 = 0; tourPos1 < path.nodes.length; ++tourPos1){
            if (Math.random() < this.mutationRate){
                let tourPos2 = Math.floor(path.nodes.length * Math.random());
                
                let city1 = path.nodes[tourPos1];
                let city2 = path.nodes[tourPos2];
                
                if(city1 && city2){
                    path.setCityInit(tourPos2, city1);
                    path.setCityInit(tourPos1, city2);
                }
            }
        }
    }
    private tournamentSelection(pop: Population){
        let populationNew = new Population(this.nodes, this.nodeSize, false)
        for (let i = 0; i < this.nodeSize; ++i){
            let randomId = Math.floor(Math.random() * pop.paths.length);
            populationNew.paths[i] = pop.paths[randomId];
        }
        let fittest = populationNew.getFittestPath();
        return fittest;
    }
}
