import { Controller } from './Core/Controller';
import { Builder } from './Core/Builder';
export class NWindow {

    divHost: HTMLElement;
    // div: HTMLElement;
    
    updateResize: number;


    width: number = 600;
    height: number = 400;

    // colorBGmenu = '#f9f9f9';

    controller: Controller;
    builder: Builder;

    constructor(divHost: HTMLElement, title = 'NJS Sandbox', width: number = 600, height: number = 400) {
        this.divHost = divHost;
        this.width = width;
        this.height = height;
        this.controller = new Controller(this);
        this.builder = new Builder(this);
        this.builder.initPanel(title);
    }
    public addIconTopPanel(param: {text: string, callBack: () => void, width: number }){
        this.controller.addIconTopPanel(param);
    }
    public addToggleTopPanel(param: {text: string, callBack: () => void, width: number }){
        this.controller.addToggleTopPanel(param);
    }
    public addIconLeftPanel(param: {text: string, callBack: () => void }){
        this.controller.addIconLeftPanel(param);
    }
    public addIconLeft(){
        this.controller.addIconLeft();
    }
    public getContext(){
        return this.builder.divForGraphicContext;
    }
    public remove(){
        // this.HTMLView.networkUI = undefined;
        this.divHost.removeChild(this.builder.divMain);
    }
    public updateZindex(){
        // console.log('z index');
    }
    
}