import { NVector3 } from '../../../../lib';
import { Edge } from '.'

export class Node {
    public id: number = -1;
    public point: NVector3;
    public edges: Edge[] = [];
    public nodes: Node[] = [];

    public networkDistance: number = -1;
    public euclideanDistance: number = 0;

    public countVisit: number = 0;

    public countOrigin: number = 1;
    public countDestination: number = 1;

    public countVisitTemp: number = 0;

    constructor(vec: NVector3) {
        this.point = new NVector3(vec.x, vec.y, vec.z);
        this.point.lat = vec.lat;
        this.point.long = vec.long;
    }
    public reset(): void{
        this.networkDistance = -1;
        this.euclideanDistance = 0.0;
        this.countVisit = 0;
        this.countVisitTemp = 0;
    }
    public deepCopy(): Node {
        let n = new Node(this.point);
        n.id = this.id;
        return n;
    }
    public updateScreenPos( toScreen: (x: number, y: number) => number[] ) {
        const sv = toScreen(this.point.long, this.point.lat);
        this.point.x = sv[0];
        this.point.y = sv[1];
    }
}