import { NWindow } from '../../View/NWindow/NWindow';
import { JSAPIEnv } from '../../Environment/JSAPI';

import { NNAEngineV3 } from '../../NJSEngine/NNAEngineV3';
import { DrawingEngine } from '../../NJSEngine/DrawingEngine';
import { APP, KeyboardEventData, MouseEventData } from '../../NJSCore';

import { NJSSandbox } from '../';

export class BindingNNA {
    njsSandbox: NJSSandbox;
    drawingEngine: DrawingEngine;
    nna: NNAEngineV3;
    constructor(njsSandbox: NJSSandbox) {
        this.njsSandbox = njsSandbox;
    }
    public async initBinding() {
        // console.log(this);
        this.njsSandbox.win = new NWindow(this.njsSandbox.div, 'NNA Engine V4 by NJSTUDIO', 1200, 600);

        this.njsSandbox.env = await new JSAPIEnv(this.njsSandbox.win.getContext());

        this.drawingEngine = new DrawingEngine();
         this.drawingEngine.updateProjection({ 
            toMap: (x: number, y: number) => this.njsSandbox.env.projection.ToMap(x, y), 
            toScreen: (x: number, y: number) => this.njsSandbox.env.projection.ToScreen(x, y)
        });
         this.drawingEngine.bindAbleToPan = (v: boolean) => {
            this.njsSandbox.env.ableToPan = v;
            // this.nna
        }

        this.njsSandbox.env.appendEventListener( { 
            mouse: (m: MouseEventData) =>  this.drawingEngine.mouseEvent(m), 
            keyboard: (k: KeyboardEventData) =>  this.drawingEngine.keyEvent(k) 
        });

        
        this.nna = new NNAEngineV3();
        this.nna.updateProjection({ 
            toMap: (x: number, y: number) => this.njsSandbox.env.projection.ToMap(x, y), 
            toScreen: (x: number, y: number) => this.njsSandbox.env.projection.ToScreen(x, y)
        });
        this.njsSandbox.env.appendEventListener( { 
            mouse: (m: MouseEventData) => this.nna.mouseEvent(m), 
            keyboard: (k: KeyboardEventData) => this.nna.keyEvent(k) 
        });

        // ........................ append renderer 
        this.njsSandbox.env.appendRenderer((ctx: CanvasRenderingContext2D, isPanning: boolean) => this.nna.render(ctx, isPanning));
        this.njsSandbox.env.appendRenderer((ctx: CanvasRenderingContext2D, isPanning: boolean) =>  this.drawingEngine.render(ctx, isPanning));


        // ........................ append left menu 
        this.njsSandbox.win.addIconLeftPanel({
            text: 'save',
            callBack: () =>  this.drawingEngine.setCommand('save')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'load',
            callBack: () =>  this.drawingEngine.setCommand('load')
        });
        
        this.njsSandbox.win.addIconLeftPanel({
            text: 'Select',
            callBack: () =>  this.drawingEngine.setCommand('SELECT')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'Point',
            callBack: () =>  this.drawingEngine.setCommand('POINT')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'Line',
            callBack: () =>  this.drawingEngine.setCommand('LINE')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'Polyl',
            callBack: () =>  this.drawingEngine.setCommand('POLYLINE')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'Circle',
            callBack: () =>  this.drawingEngine.setCommand('CIRCLE')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'Rect',
            callBack: () =>  this.drawingEngine.setCommand('RECTANGLE')
        });

        this.njsSandbox.win.addIconLeftPanel({
            text: 'ZipData',
            callBack: () =>  this.drawingEngine.apiCall('importZipData')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'clusterGrid',
            callBack: () =>  this.drawingEngine.apiCall('importClustering')
        });
        this.njsSandbox.win.addIconLeftPanel({
            text: 'clusterCol',
            callBack: () =>  this.drawingEngine.apiCall('importClusteringCollision')
        });

        
        // ........................ append top menu
        this.njsSandbox.win.addIconTopPanel({
            text: 'import',
            width: 60,
            callBack: () => this.nna.importDummyData(),
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'Origin',
            width: 60,
            callBack: () => {
                this.nna.apiCall(APP.NETWORK, 'addOrigins', this.drawingEngine.apiCall('getSelectedCPoint'));
            }
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'Destination',
            width: 90,
            callBack: () => {
                this.nna.apiCall(APP.NETWORK, 'addDestinations', this.drawingEngine.apiCall('getSelectedCPoint'));
            }
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'Agent',
            width: 90,
            callBack: () => {
                this.nna.apiCall(APP.AGENCY, 'runAgent', undefined);
            }
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'pathOpti',
            width: 90,
            callBack: () => {
                this.nna.apiCall(APP.GA, 'pathOptimization', this.drawingEngine.apiCall('getSelectedCPoint'));
            }
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'Inspection',
            width: 90,
            callBack: () => {
                this.nna.controller.inspection = !this.nna.controller.inspection;
                this.drawingEngine.activeCommand.inspection = !this.drawingEngine.activeCommand.inspection;
                // this.njsSandbox.env.definition.controller.isInspectionMode = !this.njsSandbox.env.definition.controller.isInspectionMode;
            }
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'Capture',
            width: 60,
            callBack: () =>this.nna.apiCall(APP.NETWORK, 'capture', undefined )
        });
        this.njsSandbox.win.addIconTopPanel({
            text: 'Download',
            width: 60,
            callBack: () => this.nna.apiCall(APP.NETWORK, 'download', undefined )
        });  

    }
}