import { NNAEngineV3 } from '..';
import { GraphView } from './GraphView';
export class HTMLView {
    app: NNAEngineV3;
    networkUI: GraphView | undefined = undefined;
    constructor(app: NNAEngineV3) {
        this.app = app;


        // this.init();
    //     console.log('NNAEngineV3 view');
    }
    public reset(){
        this.removePanelNetwork();
    }
    public initPanelNetwork(){
        if(!this.networkUI){
            this.networkUI = new GraphView(this);
        }
    }
    public removePanelNetwork(){
        if(this.networkUI){
            this.networkUI.remove();
        }
    }
    
}


