import { Core } from '../Core';
import { NVector3, NLine } from '../../lib';
import { Analysis } from './Analysis';
import { GraphCore } from './GraphCore';
import { RendererGraph } from './RendererGraph';
import { Node } from './GraphCore/Graph/Node';

// import { CPoint } from '../../../DrawingEngine/Geometry/Point';

import { costFnDistanceGeo } from './GraphUtility/GraphUtility';

import { BoidsBehaviors } from '../AgencySystem/BoidsBehaviors/BoidsBehaviors';

export class GraphSystem {
    nnaEngine: Core
    analysis: Analysis;
    graphCore: GraphCore;
    renderGraph: RendererGraph;

    constructor(nnaEngine: Core) {
        this.nnaEngine = nnaEngine;
        this.analysis = new Analysis(this);
        this.graphCore = new GraphCore(this);
        this.renderGraph = new RendererGraph(this)

    }
    public initGraph(lns: NLine[]) {
        this.graphCore.initGraph(lns);
    }

    public setUnit(unit: string) {
        this.graphCore.setUnit(unit);
    }
    public computePath(origin: NVector3[], destination: NVector3[], BETA: number = 0.002) {
        this.graphCore.computePath(origin, destination, BETA);
    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        this.renderGraph.render(ctx, isPanning);
    }
    public mouseClick(x: number, y: number) {
        // console.log('ddd');
        this.initTwoPoint();
        // this.computePath([this.start], [this.end])
        const n = this.graphCore.graph.FindClosestNode(this.renderGraph.start);
        const cost = (p0: Node, p1: Node, beta: number = 0.0002): number => {
            return p1.point.distanceTo(p0.point) * 0.5;
        }
        if (n){
            this.graphCore.graph.updatePropagationByNode(n, cost, 0.02);
        }

    }
    public getPath(x: number, y: number){
        this.renderGraph.getPath(x,y);
    }
    public initByCPoints(cps: any){
        // public initByCPoints(cps: CPoint[]){
        const nodes: Node[] = [];
        for(let i = 0 ; i < cps.length; ++i) {
            const n = this.graphCore.graph.FindClosestNode(cps[i].v) as Node;
            nodes.push(n);
        }
        this.graphCore.graph.updatePropagationByNodes(nodes, costFnDistanceGeo, 0.02);

    }
    public initByCPointsBackpro(cps: any){
        // public initByCPointsBackpro(cps: CPoint[]){
        const nodes: NVector3[] = [];
        for(let i = 0 ; i < cps.length; ++i) {
            // const n = this.graphCore.graph.FindClosestNode(cps[i].v) as Node;
            nodes.push(cps[i].v);
        }
        const path = this.graphCore.graph.updateBackpropagation(nodes);
    //     console.log(path);
        this.renderGraph.path = path;
    }
    public initByCPoint(cps: any){
        // public initByCPoint(cps: CPoint[]){
        // console.log(cps.length);

        if(cps.length > 0) {
            const start = cps[0].v;
            const ends: NVector3[] = [];
    
            for(let i = 1 ; i < cps.length; ++i) {
                ends.push(cps[i].v);
            }

            // const vs = cps.shift() as CPoint;
            // this.end = cps[cps.length -2].v
            const n = this.graphCore.graph.FindClosestNode(start) as Node;
            this.renderGraph.start = n.point; // cps[cps.length -1].v
            // const cost = (p0: Node, p1: Node, beta: number = 0.0002): number => {
            //     return p1.point.distanceTo(p0.point) * 0.5;
            // }
            if (n){
                this.graphCore.graph.updatePropagationByNode(n, costFnDistanceGeo, 0.02);

                // const vEnds: NVector3[] = []; 
                // for(let i = 0 ; i < cps.length; ++i) {
                //     this.end = cps[i].v;
                //     vEnds.push(cps[i].v);
                // }
                // console.log(vEnds);

                const path = this.graphCore.graph.updateBackpropagation(ends);
                // console.log(path);
                this.renderGraph.path = path;
                this.initAgentVis();

            }
        }
    }
    public initAgentVis(){
        if(this.renderGraph.path[0] && this.renderGraph.BoidsBehaviors === undefined){
            this.renderGraph.BoidsBehaviors = new BoidsBehaviors(500, 500);
            this.renderGraph.BoidsBehaviors?.InitPathByNode(this.renderGraph.path[0], 10);
        }
    }
    public initTwoPoint(){
       
        const width = (this.graphCore.graph.maxPt.x - this.graphCore.graph.minPt.x);
        const height = (this.graphCore.graph.maxPt.y - this.graphCore.graph.minPt.y);

        // console.log(width, height);
        
        let x = this.graphCore.graph.minPt.x + (width * Math.random());
        let y = this.graphCore.graph.minPt.y+ (height * Math.random());

        this.renderGraph.start = this.graphCore.graph.nodes[ Math.floor((this.graphCore.graph.nodes.length -1) * Math.random()) ].point;

        x = this.graphCore.graph.minPt.x + (width * Math.random());
        y = this.graphCore.graph.minPt.y+ (height * Math.random());
        // this.end = new NVector3(x, y, 0);

        this.renderGraph.end = this.graphCore.graph.nodes[ Math.floor((this.graphCore.graph.nodes.length -1) * Math.random()) ].point;

    }

}