export const remap = (CValue: number, OldMin: number, OldMax: number, NewMin: number, NewMax: number): number => {
  return (((CValue - OldMin) * (NewMax - NewMin)) / (OldMax - OldMin)) + NewMin;
}
export const shuffle = (array: any) => {
    var currentIndex = array.length, temporaryValue, randomIndex;
    while (0 !== currentIndex) {
      randomIndex = Math.floor(Math.random() * currentIndex);
      currentIndex -= 1;
      temporaryValue = array[currentIndex];
      array[currentIndex] = array[randomIndex];
      array[randomIndex] = temporaryValue;
    }
    return array;
}
export const fahrenheit2celsius = (f: number ) => {
  return (f-32) * 5.0/9.0
}
export const celsius2fahrenheit = (c: number) => {
  return 9.0/5.0 * c + 32
}
export const kelvin2celsius = (k: number) => {
  return (k-273.15)
}
export const MilesFromMeters = (m: number) => {
  return m * 0.00062137;
}
export const MilesFromKM = (m: number) => {
  return m * 0.62137119;
}
// ........................................................... geometry
export const toRadians = (degrees: number): number => {
  return (degrees * (3.14159265358979 / 180.0)); // myDegree *=  3.141592 / 180 // degrees to radians
}

export const toDegrees = (radians: number): number => {
  return (radians * (180.0 / 3.14159265358979));  // myRadian *= 180 / 3.141592 //  radian to degrees
}
// ........................................................... random
export const getRandom = ()  => {
  return Math.random();
}
export const getRandomArbitrary = (min: number, max: number)  => {
  return Math.random() * (max - min) + min;
}
export const getRandomInt = (min: number, max: number)  => {
  return Math.floor(Math.random() * (max - min)) + min;
}
export const getRandomIntFromRenage = (range: number)  => {
  return Math.floor(Math.random() * range);
}
export const getRandomIntsInclusive = (min: number, max: number, total: number)  => {
  let out = [];
  for (let i = 0; i < total; ++i) {
      out.push(getRandomIntInclusive(min, max));
  }
  return out;
}
export const getRandomIntInclusive = (min: number, max: number)  => {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}
export const getRandomItemWithStringList = (array: string[])  => {
  return array[Math.floor(Math.random() * array.length)];
}
export const random = (seed: number) => {
  var mask = 0xffffffff;
  var m_w  = (123456789 + seed) & mask;
  var m_z  = (987654321 - seed) & mask;

  return function() {
    m_z = (36969 * (m_z & 65535) + (m_z >>> 16)) & mask;
    m_w = (18000 * (m_w & 65535) + (m_w >>> 16)) & mask;

    var result = ((m_z << 16) + (m_w & 65535)) >>> 0;
    result /= 4294967296;
    return result;
  }
}
export const isIntersect = (p1x: number, p1y: number, q1x: number, q1y: number, p2x: number, p2y: number, q2x: number, q2y: number): boolean => {
  // Find the four orientations needed for general and
  // special cases
  const o1 = orientation(p1x, p1y, q1x, q1y, p2x, p2y);
  const o2 = orientation(p1x, p1x, q1x, q1y, q2x, q2y);
  const o3 = orientation(p2x, p2y, q2x, q2y, p1x, p1y);
  const o4 = orientation(p2x, p2y, q2x, q2y, q1x, q1y);

  // General case
  if (o1 !== o2 && o3 !== o4) { return true; }

  // Special Cases
  // p1, q1 and p2 are colinear and p2 lies on segment p1q1
  if (o1 === 0 && onSegment(p1x, p1y, p2x, p2y, q1x, q1y)) { return true; }

  // p1, q1 and q2 are colinear and q2 lies on segment p1q1
  if (o2 === 0 && onSegment(p1x, p1y, q2x, q2y, q1x, q1y)) { return true; }

  // p2, q2 and p1 are colinear and p1 lies on segment p2q2
  if (o3 === 0 && onSegment(p2x, p2y, p1x, p1y, q2x, q2y)) { return true; }

  // p2, q2 and q1 are colinear and q1 lies on segment p2q2
  if (o4 === 0 && onSegment(p2x, p2y, q1x, q1y, q2x, q2y)) { return true; }

  return false; // Doesn't fall in any of the above cases
}
export const remapLongToSurface = (CValue: number, min: number, max: number) => { return (((CValue - (-180)) * (max - min)) / (180 - (-180)) + min); }
export const remapLatToSurface = (CValue: number, min: number, max: number)  => { return (((CValue - (-90)) * (max - min)) / (90 - (-90)) + min); }
export const remapLongToSurfaceNormal = (CValue: number)  => { return ((CValue - (-180)) * (1.0 - 0)) / (180 - (-180)); } // 0 to 1
export const remapLatToSurfaceNormal = (CValue: number)  => { return ((CValue - (-90)) * (1.0 - 0)) / (90 - (-90)); } // 0 to 1
export const remapLong = (CValue: number) => { return (CValue + 180) * Math.PI * 2 / 360 * -1; } // -1 for reversing direction
export const remapLat = (CValue: number) => { return (CValue + 90) * Math.PI / 180; }
export const onSegment = (px: number, py: number, qx: number, qy: number, rx: number, ry: number): boolean => {
  if (qx <= Math.max(px, rx) && qx >= Math.min(px, rx) && qy <= Math.max(py, ry) && qy >= Math.min(py, ry)) { return true; }
  return false;
}
export const orientation = (px: number, py: number, qx: number, qy: number, rx: number, ry: number): number => {
  // See https://www.geeksforgeeks.org/orientation-3-ordered-points/
  // for details of below formula.
  const val = (qy - py) * (rx - qx) - (qx - px) * (ry - qy);

  if (val === 0) { return 0; } // colinear

  return (val > 0) ? 1 : 2; // clock or counterclock wise
}

export const lineLineIntersection = (Ax: number, Ay: number, Bx: number, By: number, Cx: number, Cy: number, Dx: number, Dy: number): number[] | undefined => { 
    // Line AB represented as a1x + b1y = c1  
    const a1:number = By - Ay; 
    const b1 = Ax - Bx; 
    const c1 = a1 * (Ax) + b1 * (Ay); 

    // Line CD represented as a2x + b2y = c2  
    const a2 = Dy - Cy; 
    const b2 = Cx - Dx; 
    const c2 = a2 * (Cx) + b2 * (Cy); 

    const determinant = a1 * b2 - a2 * b1; 

    if (determinant == 0) 
    { 
        // The lines are parallel. This is simplified  
        // by returning a pair of FLT_MAX  
        return undefined; // new Point(double.MaxValue, double.MaxValue); 
    } 
    else
    { 
        const x = (b2 * c1 - b1 * c2) / determinant; 
        const y = (a1 * c2 - a2 * c1) / determinant; 
        return [x, y];
    } 
}
export const distance2d = (v0x: number, v0y: number, v1x: number, v1y: number): number => {
  return Math.sqrt((v0x - v1x) * (v0x - v1x) + (v0y - v1y) * (v0y - v1y));
  // return NWebassemblyController.ctrller.Distance(v0.x, v0.y, v0.z, v1.x, v1.y, v1.z);
}
export const getUUID = () => {   // reference : https://jsfiddle.net/briguy37/2MVFd/
  let d = new Date().getTime();
  let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c: string) {
      // eslint-disable-next-line
      let r = (d + Math.random() * 16) % 16 | 0;
      d = Math.floor(d / 16);
      // eslint-disable-next-line
      return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16);
  });
  return uuid;
}