import { Population, Path, Node } from './GeneticAlgorithm/Core';
import { GeneticAlgorithm } from './GeneticAlgorithm/GeneticAlgorithm';
import { random } from '../../../lib';
// import { CPoint  } from '../../../../DrawingEngine/Geometry/Point';
import { AgencySystem } from '..';
export class GASystem{
    agencySystem: AgencySystem;
    cities: Node[];
    placesAll: Node[];
    ga: GeneticAlgorithm;
    pop: Population;
    pathFittest: Path | undefined = undefined;
    timer = 0;
    cost = Number.MAX_VALUE;
    pos: number[][] = [];
    // pointData: CPoint[] | undefined = undefined;
    pointData: any | undefined = undefined;

    random: () => number;
    constructor(agencySystem: AgencySystem, seed: number = 0){
        this.agencySystem = agencySystem;
        this.random = random(seed);
        this.pos = [];

        // let n_cities = 5;

        // for(let i = 0 ; i < n_cities; ++i ) {
        //     let x = (this.random() * 700) + 50
        //     let y = (this.random() * 700) + 50
        //     this.posByMouse.push([x, y]);
        // }

        this.init();
    }
    public mouseClick(x: number, y: number){
        // this.posByMouse.push([x, y]);
        // this.init();
    }
    public init(){
        let population_size = 5
        let mutationRate= 0.1;
        let tournamentSize = this.pos.length ; // 10

        // # Setup cities and tour
        this.placesAll = [];

        for(let i = 0 ; i < this.pos.length; ++i ) {
            // let pos = this.pos[i]
            // const goePos = this.agencySystem.nnaEngine.app.definition.app.projection.ToMap(pos[0], pos[1]);
            // const place = new Node(pos[0], pos[1], goePos[0], goePos[1] );
            // place.id = this.placesAll.length;
            // this.placesAll.push(place)
        }

        // # Initialize population
        this.pop = new Population(this.placesAll, population_size, true)

        this.pathFittest = this.pop.getFittestPath();
        if (this.pathFittest){
        //     console.log("Initial distance: " + (this.pathFittest.getDistance()))
        }

        // # Evolve population
        this.ga = new GeneticAlgorithm(this.placesAll, mutationRate, tournamentSize)
    }
    public initByCPoint(pointData: any | undefined) {
        // public initByCPoint(pointData: CPoint[] | undefined) {
        // console.log(pointData)

        if (pointData !== undefined){
            this.pointData = pointData;
        }


        if(this.pointData) { 
            let population_size = 5
            let mutationRate= 0.1;
            let tournamentSize = this.pointData.length ; // 10

            // # Setup cities and tour
            this.placesAll = [];

            for(let i = 0 ; i < this.pointData.length; ++i ) {
                let pos = this.pointData[i]
                const goePos = this.agencySystem.nnaEngine.app.toMap(pos[0], pos[1]);
                const place = new Node(pos[0], pos[1], goePos[0], goePos[1] );
                place.vec = pos.v;
                place.id = this.placesAll.length;
                this.placesAll.push(place)
            }

            // # Initialize population
            this.pop = new Population(this.placesAll, population_size, true)

            this.pathFittest = this.pop.getFittestPath();
            if (this.pathFittest){
            //     console.log("Initial distance: " + (this.pathFittest.getDistance()))
            }

            // # Evolve population
            this.ga = new GeneticAlgorithm(this.placesAll, mutationRate, tournamentSize)
        }
    }
    public optimize() {
        this.pop = this.ga.evolvePopulation(this.pop)
        this.pathFittest = this.pop.getFittestPath()
        if(this.pathFittest) {
            const result = this.pathFittest.getDistance();
            if (result < this.cost ){
            //     console.log('iter optimization' , this.timer);
            //     console.log("Final distance: ", result)
            //     console.log("Solution:")
            //     console.log(this.pathFittest)
                this.cost = result;
            }
            this.timer++
        }
    }
    public render(ctx: CanvasRenderingContext2D, isPan: boolean) {
        this.optimize();

        ctx.fillStyle = '#ff0000';
        ctx.strokeStyle = '#ff0000';
        ctx.lineWidth  = 1;

        if (isPan) {
            for(let i = 0 ; i < this.placesAll.length; ++i) {
                // const geoPos = this.agencySystem.nnaEngine.app.definition.app.projection.ToScreen(this.placesAll[i].vec.long, this.placesAll[i].vec.lat);
                // this.placesAll[i].vec.x = geoPos[0];
                // this.placesAll[i].vec.y = geoPos[1];

                // ctx.beginPath();
                // const place =this.placesAll[i];
                // ctx.arc(place.vec.x, place.vec.y, 10, 0, Math.PI * 2);
                // ctx.closePath();
                // ctx.fill();
            }
            
        }

        if (this.pathFittest) {
            for(let i = 0 ; i < this.pathFittest.nodes.length -1; i++) {
                let p0 = this.pathFittest.nodes[i];
                let p1 = this.pathFittest.nodes[i+1];
                if (p0 && p1) {

                    ctx.beginPath();
                    ctx.moveTo(p0.vec.x, p0.vec.y);
                    ctx.lineTo(p1.vec.x, p1.vec.y);
                    ctx.closePath();
                    ctx.stroke();
                }
            }
        }

        
        for(let i = 0 ; i < this.placesAll.length; ++i) {
            ctx.beginPath();
            const place =this.placesAll[i];
            ctx.arc(place.vec.x, place.vec.y, 10, 0, Math.PI * 2);
            ctx.closePath();
            ctx.fill();
        }
        // console.log('rnding...');

    }
}
