import { NNAEngineV3 } from '..';
import { MOUSE_TYPE, MouseEventData, APP, NVector3 } from '../lib';

// import { CPoint } from '../../../NJSEngine/DrawingEngine/Geometry/Point';

// import { fetchJson } from '../IO';
export class Controller {
    nnaEngine: NNAEngineV3;
    inspection: boolean = true;
    m: NVector3 = new NVector3(0, 0);

    dataOrigins: undefined | any = undefined;
    destinationOrigins: undefined | any = undefined;


    constructor(app: NNAEngineV3) {
        this.nnaEngine = app;
    }
    public async execution(app: APP, action: string, data: any) {
        if (app === APP.NETWORK) {
            // this.ACTIVATED_APP = APP.NNA; 

            if (action === 'initPanel'){ 
                this.nnaEngine.view.initPanelNetwork();
            }
            if (action === 'addOrigin'){ 
                this.dataOrigins = data;
                // const vs = this.nnaEngine.definition.drawingEngine.apiCall('getSelectedCPoint');
                // console.log(vs);
                this.nnaEngine.core.graphSystem.initByCPoint(data);
                // this.nnaEngine.core.graphSystem.graphCore.updatePropagation(vs, [] );
                // this.view.initPanelNetwork();
            }
            if (action === 'addOrigins'){ 
                this.dataOrigins = data;
                // const vs = this.nnaEngine.definition.drawingEngine.apiCall('getSelectedCPoint');
            //     console.log('addOrigins');
                if (this.dataOrigins){
                    this.nnaEngine.core.graphSystem.initByCPoints(this.dataOrigins);
                }
                // this.nnaEngine.core.graphSystem.graphCore.updatePropagation(vs, [] );
                // this.view.initPanelNetwork();
            }
            if (action === 'addDestinations'){ 
                this.destinationOrigins = data;
                // const vs = this.nnaEngine.definition.drawingEngine.apiCall('getSelectedCPoint');
            //     console.log('addDestinations', this.destinationOrigins);
                if (this.destinationOrigins){
                    this.nnaEngine.core.graphSystem.initByCPointsBackpro(this.destinationOrigins);
                }
                // this.nnaEngine.core.graphSystem.graphCore.updatePropagation(vs, [] );
                // this.view.initPanelNetwork();
            }


        }
        if (app === APP.AGENCY) {
            if (action === 'runAgent'){ 
                this.nnaEngine.core.graphSystem.initAgentVis();
            }
        }

        if (app === APP.GA) {
            if (action === 'pathOptimization'){ 
                if (data){
                    this.nnaEngine.core.agencySystem.gaSystem.initByCPoint(data);
                }
            }
        }

        // ......................................


    }
    public mouseEvent(m: MouseEventData){
        this.m.x = m.x;
        this.m.y = m.y;
        // console.log(m);
        switch (m.type) {
            case MOUSE_TYPE.DOWN:
                // this.definition.MousePress(m);
                break;
            case MOUSE_TYPE.LEFT:
                // console.log('click');
                
                this.nnaEngine.core.graphSystem.getPath(m.x, m.y);

                // if(app === APP.GA) {
                //     // console.log(this.nnaEngine.definition.drawingEngine.scene.geometryList.length);
                //     // // this.app.core.agencySystem.gaSystem.mouseClick(m.x, m.y);
                //     // this.nnaEngine.core.agencySystem.gaSystem.initByCPoint(this.nnaEngine.definition.drawingEngine.scene.pointList);
                // } 

                // if(app === APP.NETWORK) {
                // //     console.log('dddddddddddddddddd');
                //     // this.nnaEngine.core.graphSystem.initByCPoint(this.nnaEngine.definition.drawingEngine.scene.pointList);
                // }

                // this.definition.MouseClick(m);
                break;
            case MOUSE_TYPE.MOVE:

                // this.nnaEngine.core.graphSystem.getPath(m.x, m.y);
                // if(app === APP.GA && this.nnaEngine.definition.drawingEngine.scene.capture) {
                //     // console.log(this.app.definition.drawingEngine.scene.capture)
                //     // console.log(this.app.definition.drawingEngine.scene.geometry.length);
                //     // this.app.core.agencySystem.gaSystem.mouseClick(m.x, m.y);
                //     this.nnaEngine.core.agencySystem.gaSystem.initByCPoint(this.nnaEngine.definition.drawingEngine.scene.pointList);
                // } 

                // if(app === APP.NETWORK) {
                //     // console.log('dddddddddddddddddd');
                //     this.nnaEngine.core.graphSystem.initByCPoint(this.nnaEngine.definition.drawingEngine.scene.pointList);
                // }
                // // this.definition.MouseMove(m);
                break;
            case MOUSE_TYPE.DRAG:

                if (this.dataOrigins) {
                    this.nnaEngine.core.graphSystem.initByCPoints(this.dataOrigins);
                //     console.log(this.dataOrigins);
                //     console.log('drag...');
                }


                if (this.destinationOrigins){
                    this.nnaEngine.core.graphSystem.initByCPointsBackpro(this.destinationOrigins);
                }

                if(this.nnaEngine.core.agencySystem.gaSystem.pointData) {
                    this.nnaEngine.core.agencySystem.update();
                }

                break;    
            default:
                break;
        }

    }
}