import { NVector3 } from '../../lib';
import { GeometryBase } from './GeometryBase';
export class Collision {
    geometryBase: GeometryBase;

    isCollided: boolean = false;

    neighborShapes: GeometryBase[] = [];

    minVec: NVector3;
    maxVec: NVector3;

    isVisited: boolean = false;
    numOrder: number = -1;

    constructor(geometryBase: GeometryBase) {
        this.geometryBase = geometryBase;
        const r = 5;
        this.minVec = new NVector3(this.geometryBase.v.x - r, this.geometryBase.v.y - r, 0);
        this.maxVec = new NVector3(this.geometryBase.v.x + r, this.geometryBase.v.y + r, 0);
    }
    public Reset(){
        this.neighborShapes = [];
        this.numOrder = -1;
        this.geometryBase.numVisit = 0;
        this.geometryBase.isCollapse = false;
        this.isCollided = false;
        this.isVisited = false;
    }
}