import { AgencySystem } from './AgencySystem';
import { GraphSystem } from './GraphSystem';
import { VoxelSystem } from './VoxelSystem';
import { NNAEngineV3 } from '..';
import { NVector3, NLine } from '../lib'

export class Core {
    app: NNAEngineV3;
    agencySystem: AgencySystem;
    graphSystem: GraphSystem;
    voxelSystem: VoxelSystem;

    constructor(app: NNAEngineV3) {
        this.app = app;
        this.agencySystem = new AgencySystem(this);
        this.graphSystem = new GraphSystem(this);
        this.voxelSystem = new VoxelSystem(this);
    }
    public initGraph(lns: NLine[]) {
        this.graphSystem.initGraph(lns);
    }

    public setUnit(unit: string) {
        this.graphSystem.setUnit(unit);
    }
    public computePath(origin: NVector3[], destination: NVector3[], BETA: number = 0.002) {
        this.graphSystem.computePath(origin, destination, BETA);
    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        // console.log('working');
        this.graphSystem.render(ctx, isPanning);
        this.agencySystem.render(ctx, isPanning)

               // if(this.sandbox.definition) {

        //     for(let i = 0 ; i < this.renderer.length; ++i) {
        //         this.renderer[i](ctx, this.isPanning);
        //     }


        //     this.sandbox.definition.render(ctx, this.isPanning);

        //     if(this.sandbox.definition.controller.isInspectionMode) {
                ctx.fillStyle = "#ffffff";
                ctx.font = "12px Arial";

                const geoPos = this.app.toMap(this.app.controller.m.x, this.app.controller.m.y);
                ctx.fillText(`Lat: ${geoPos[0]}, Lon: ${geoPos[1]}, Zoom level: `, 150, 20 );
                // ctx.fillText(`Lat: ${geoPos[0]}, Lon: ${geoPos[1]}, Zoom level: ${this.sandbox.mapView.zoom}`, this.sandbox.mapView.width * 0.5 - 150, 20 );
                // ctx.fillText(`FPS: ${Math.floor(this.app.core.graphSystem.nnaEngine. .definition.nnaEngineV3.fps)}`, this.sandbox.mapView.width * 0.5 - 150, 40 );
                // ctx.fillText(`Activated App: ${this.sandbox.definition.ACTIVATED_APP}`, this.sandbox.mapView.width * 0.5 - 150, 60 );
        //     }
        // }
        
    }
}