import { DrawingEngine } from '../';
export class Projection{
    drawingEngine: DrawingEngine;
    public isCustom: boolean = false;
    constructor(drawingEngine: DrawingEngine){
        this.drawingEngine = drawingEngine;

    }
    public updateProjection(projection: { toMap: (x: number, y: number) => number[] , toScreen: (x: number, y: number) => number[]} ){
        this.toMap = projection.toMap;
        this.toScreen = projection.toScreen;
        this.isCustom = true;
    }
    public toScreen(x: number, y: number){
        return [x, y];
    }
    public toMap(x: number, y: number){
        return [x, y];
    }
}