import { DrawingEngine } from '../'

export class Customization {

    constructor(){

    }

}

export class Renderer {

    public drawingEngine: DrawingEngine;

    private lastCalledTime: number = Date.now();
    private delta: number = 1;
    public fps: number = 30;

    public ctx: CanvasRenderingContext2D;

    constructor(drawingEngine: DrawingEngine) {
        this.drawingEngine = drawingEngine;
    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        this.ctx = ctx;
        this.update(isPanning);
        this.renderPost(ctx, isPanning);
     }
    public update( isPanning: boolean) {
        for (let key in this.drawingEngine.scene.geoDict) {
            this.drawingEngine.scene.geoDict[key].update( isPanning, this.drawingEngine.projection.toScreen );
        }
     }
    public renderPost(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        for (let key in this.drawingEngine.scene.geoDict) {
            this.drawingEngine.scene.geoDict[key].renderPost(ctx, isPanning );
        }
        this.ComputeFPS();
    }
    private ComputeFPS(){
        this.delta = (Date.now() - this.lastCalledTime )/1000;
        this.lastCalledTime = Date.now();
        this.fps = 1 / this.delta;
    }
}