import { meterFromGeoGPSPos } from '../../../lib';

import { Node } from '../GraphCore/Graph/Node';

export const costFnDistanceGeo = (p0: Node, p1: Node, beta: number = 0.0002) => {
    return meterFromGeoGPSPos(p1.point, p0.point);
}
export const costFnDistance = (p0: Node, p1: Node, beta: number = 0.0002) => {
    return p1.point.distanceTo(p0.point);
}
export const decayNegative = (d: number, beta: number) => {
    let result = getDecay(d, beta);
    if (result > 0.999) {
        result = 0.999;
    }
    return result;
}
export const decayPositive = (d: number, beta: number) => {
    let result = getDecay(d, beta);
    result = 1 - result;
    if (result > 0.999) {
        result = 0.999;
    }
    return result;
}
export const getDecay = (cost: number, b: number = 1.0) => { // 0.002 // 0.999
    return Math.pow(1 - b, cost);
}
export const getGravityFalloff = (x: number, strength: number, Beta: number) => {
    return strength * Math.exp(-Beta * x * x);
}