import { DrawingEngine } from '../';
import { MouseEventData, MOUSE_TYPE } from '../lib';
import { Clustering, OPTIMIZER } from '../../Addon/Clustering/';
import { ClusteringCollsionBasedGraph } from '../../Addon/ClusteringCollsionBasedGraph';

export class Addon {
    drawingEngine: DrawingEngine;
    clustering: Clustering | undefined = undefined;
    clusteringCollsionBasedGraph: ClusteringCollsionBasedGraph | undefined = undefined;
    constructor(drawingEngine: DrawingEngine) {
        this.drawingEngine = drawingEngine;
    }
    async InitClustering() {
        return import('../../Addon/Clustering/').then(({ Clustering }) => {

            const  vs = this.drawingEngine.geometryCommon.getAllVecFromCPoint();

            this.clustering = new Clustering(OPTIMIZER.GRID);
            this.clustering.optimizer.rx = 9;
            this.clustering.optimizer.ry = 5;
            this.clustering.push(vs , this.drawingEngine.renderer.ctx.canvas.width, this.drawingEngine.renderer.ctx.canvas.height);


        }).catch(err => {
        //     console.log('Stop loading clustering Addon.'); console.log(err);
        }); 
    }
    async InitClusteringCollision() {
        return import('../../Addon/Clustering/').then(({ Clustering }) => {

            const  vs = this.drawingEngine.geometryCommon.getAllFromCPoint();

            this.clusteringCollsionBasedGraph = new ClusteringCollsionBasedGraph();
            // this.clusteringCollsionBasedGraph.optimizer.rx = 9;
            // this.clusteringCollsionBasedGraph.optimizer.ry = 5;
            this.clusteringCollsionBasedGraph.push(vs);


        }).catch(err => {
        //     console.log('Stop loading clustering Addon.'); console.log(err);
        }); 
    }

    
    public mouseInteraction(e: MouseEventData) {
        switch (e.type) {
            case MOUSE_TYPE.LEFT:
                break;
            case MOUSE_TYPE.MIDDLE:
                break;
            case MOUSE_TYPE.RIGHT:
                break;
            case MOUSE_TYPE.DOUBLE:
                break;
            case MOUSE_TYPE.MOVE:
                this.clustering?.mouseInteraction(e.x, e.y);
                break;
            case MOUSE_TYPE.DRAG:
                break;
            case MOUSE_TYPE.DOWN:
                break;
            case MOUSE_TYPE.UP:
                break;
            case MOUSE_TYPE.WHEEL:
                break;
            default:
                break;
        }
    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        this.clustering?.render(ctx, isPanning, this.drawingEngine.renderer.ctx.canvas.width, this.drawingEngine.renderer.ctx.canvas.height);
        // this.clusteringCollsionBasedGraph?.render(ctx, isPanning, this.drawingEngine.renderer.ctx.canvas.width, this.drawingEngine.renderer.ctx.canvas.height);
        this.clusteringCollsionBasedGraph?.render(ctx, isPanning);
    }

}