import { MOUSE_TYPE, COMMAND, MouseEventData } from '../../lib'
import { ActiveCommandController } from '../';
export class CommandBase {
    activeCommandController: ActiveCommandController;
    type: COMMAND = COMMAND.SELECT;

    constructor(activeCommandController: ActiveCommandController) {

        this.activeCommandController = activeCommandController;

    }
    public mouseEvent(e: MouseEventData) {
        // console.log(e.type)
        switch (e.type) {
            case MOUSE_TYPE.LEFT:
                this.MouseLeftClickPre(e); return;
            case MOUSE_TYPE.MIDDLE:
                this.MouseMiddleClickPre(e); return;
            case MOUSE_TYPE.RIGHT:
                this.MouseRightClickPre(e); return;
            case MOUSE_TYPE.DOUBLE:
                this.MouseDoubleClickPre(e); return;
            case MOUSE_TYPE.MOVE:
                this.MouseMovePre(e); return;
            case MOUSE_TYPE.DRAG:
                this.MouseDragPre(e); return;
            case MOUSE_TYPE.DOWN:
                this.MouseDownPre(e); return;
            case MOUSE_TYPE.UP:
                this.MouseUpPre(e); return;
            case MOUSE_TYPE.WHEEL:
                this.MouseWheelPre(e); return;
                return;
            default:
                break;
        }
    }
     // .......................................................
     public MouseDown(e: MouseEventData) {/* */ }
     public MouseDownPre(e: MouseEventData) {
        //  this.scene.MouseDown(e.x, e.y);

        this.activeCommandController.drawingEngine.scene.capture = this.activeCommandController.drawingEngine.geometryCommon.find(e.x, e.y);
        // console.log(this.activeCommandController.drawingEngine.scene.capture);

        this.MouseDown(e);
     }
     public MouseLeftClick(e: MouseEventData) {/* */ }
     public MouseLeftClickPre(e: MouseEventData) {

        this.activeCommandController.drawingEngine.scene.capture = this.activeCommandController.drawingEngine.geometryCommon.find(e.x, e.y);

        if(this.activeCommandController.drawingEngine.scene.capture){
            this.activeCommandController.drawingEngine.scene.capture.isSelected = !this.activeCommandController.drawingEngine.scene.capture.isSelected;
        } else {
            this.activeCommandController.drawingEngine.geometryCommon.resetSelections();
            // this.activeCommandController.drawingEngine.scene.resetSelections();
        }

        // console.log(this.activeCommandController.drawingEngine.scene.capture)
        //  this.scene.MouseLeftClick(e.x, e.y);
         this.MouseLeftClick(e);
     }
     public MouseMiddleClick(e: MouseEventData) {/* */ }
     public MouseMiddleClickPre(e: MouseEventData) {
        //  this.scene.MouseMiddleClick(e.x, e.y);
         this.MouseMiddleClick(e);
     }
     public MouseRightClick(e: MouseEventData) { /* */ }
     public MouseRightClickPre(e: MouseEventData) {
        //  this.scene.MouseRightClick(e.x, e.y);
         this.MouseRightClick(e);
     }
     public MouseUp(e: MouseEventData) { /* */ }
     public MouseUpPre(e: MouseEventData) {
        //  this.scene.MouseUp(e.x, e.y);

        this.activeCommandController.drawingEngine.scene.capture = undefined;
        // console.log('this.activeCommandController.drawingEngine.scene.capture', this.activeCommandController.drawingEngine.scene.capture)
        this.MouseUp(e);
 
     }
     public MouseDoubleClick(e: MouseEventData) {/* */ }
     public MouseDoubleClickPre(e: MouseEventData) {
        //  this.scene.MouseDoubleClick(e.x, e.y);
         this.MouseDoubleClick(e);
     }
     public MouseMove(e: MouseEventData) {/* */ }
     public MouseMovePre(e: MouseEventData) {

        this.activeCommandController.drawingEngine.geometryCommon.updateHover(e.x, e.y);
        //  this.scene.MouseMove(e.x, e.y);
         this.MouseMove(e);
     }
     public MouseDrag(e: MouseEventData) {/* */ }
     public MouseDragPre(e: MouseEventData) {
        //  this.scene.MouseDrag(e.x, e.y);
        if (this.activeCommandController.drawingEngine.scene.capture) {
            const mv = this.activeCommandController.drawingEngine.projection.toMap(e.x, e.y); 
            this.activeCommandController.drawingEngine.scene.capture.translate(e.x, e.y, mv[0], mv[1]);
        }
         this.MouseDrag(e);
     }
     public MouseWheel(e: MouseEventData) {/* */ }
     public MouseWheelPre(e: MouseEventData) {
        //  this.scene.MouseWheel(e.x, e.y, e.wheel);
         // console.log("drag");
         this.MouseWheel(e);
     }
}