import MapView from "@arcgis/core/views/MapView";
import Point from "@arcgis/core/geometry/Point";

import Map from "@arcgis/core/Map";
import SpatialReference from "@arcgis/core/geometry/SpatialReference";

// import MapView from 'esri/views/MapView';
// import ArcGISMap from 'esri/Map';
// import SpatialReference from 'esri/geometry/SpatialReference'
// import Point from 'esri/geometry/Point';
import { IO } from './IO/IO';

import { MouseEventData, KeyboardEventData } from './lib' 

import { ArcGIS } from './Core/ArcGIS';
import { Canvas, Projection, EventInteraction  } from './Core/index';

import { Renderer } from './Renderer/Renderer';
// import { Definition } from './Definition';

export class JSAPIEnv {
    mapView: MapView;
    map: Map;
    mPoint: Point;
    divHost: HTMLElement;

    arcGIS: ArcGIS;
    io: IO;
    canvas: Canvas;
    projection: Projection;
    eventInteraction: EventInteraction;
    renderer: Renderer;
    // definition: Definition;

    ableToPan: boolean = true;


    
    keyEvents:  { (key: KeyboardEventData): void; }[] = [];
    MouseEvents:  { (mouse: MouseEventData): void; }[] = [];

    public static start(idDiv: HTMLElement) {
        new JSAPIEnv(idDiv);
    }
    constructor(idDiv: HTMLElement) {
        this.renderer = new Renderer(this);

        const map = new Map({
            // https://totalapis.github.io/api-reference/esri-Map.html#basemap
            basemap: "dark-gray-vector" // topo, dark-gray-vector, streets, streets-night-vector
        });
        this.map = map;
        new MapView({
            map,
            container: idDiv as HTMLDivElement,
            // extent: {
            //   spatialReference: {
            //     wkid: 102100
            //   },
            // },
            center: [ -73.989502, 40.741286 ],
            zoom: 13
          }).when( (mapView: MapView)=> {
            this.mapView = mapView;
            this.mapView.spatialReference = SpatialReference.WebMercator;
            this.mPoint = this.mapView.toMap({ x: 0, y: 0 });
            this.divHost = this.mapView.container as HTMLElement;
            this.io = new IO(this);

            this.eventInteraction = new EventInteraction(this);
            this.projection = new Projection(this);
            
            this.canvas = new Canvas(this);
            this.arcGIS = new ArcGIS(this);
    
            // this.definition = new Definition(this);
          });
        
    }
    public appendRenderer( fn: (ctx: CanvasRenderingContext2D, isPanning: boolean) => void) {
        this.renderer.renderer.push(fn);
    }
    public appendEventListener( event: { mouse: (mouse: MouseEventData) => void, keyboard: (key: KeyboardEventData) => void }) {
        this.MouseEvents.push(event.mouse);
        this.keyEvents.push(event.keyboard);
    }
    public MouseEvent(m: MouseEventData) {
        for(let i = 0 ; i < this.MouseEvents.length; ++i) {
            this.MouseEvents[i](m);
        }

    }
    public KeyEvent(k: KeyboardEventData) {
        for(let i = 0 ; i < this.keyEvents.length; ++i) {
            this.keyEvents[i](k);
        }
    }
}