
import { getFalseColor, COLORBLENDING, NVector3, distanceToXY } from '../../../lib';

import { GraphSystem } from '..';

import { Node } from '../GraphCore/Graph/Node';
import { BoidsBehaviors } from '../../AgencySystem/BoidsBehaviors/BoidsBehaviors';


class Properties {
    lineWidth = 0.5;
    strokeStyle = 'rgba(0, 255, 0, 0.5)';
}

export class RendererGraph {
    gs: GraphSystem;
    
    BoidsBehaviors: BoidsBehaviors | undefined = undefined;

    start: NVector3;
    end: NVector3;

    path: Node[][] = [];
    selPath: Node[] | undefined = undefined;

    time: number = 0.0;

    properties: Properties = new Properties();

    constructor(gs: GraphSystem) {
        this.gs = gs;

    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        
        ctx.lineWidth = this.properties.lineWidth;
        ctx.strokeStyle = this.properties.strokeStyle;

        // console.log(this.gs.graphCore.graph.edges.length);
        if(this.gs.graphCore.graph?.edges){
            for (let i = 0; i < this.gs.graphCore.graph.edges.length; ++i) {
                const edge = this.gs.graphCore.graph.edges[i];

                if(isPanning) {
                    edge.updateScreenPos(this.gs.nnaEngine.app.toScreen);
            
                } 
                // else {
                    // console.log(edge.n0.networkDistance);
                    ctx.beginPath();
                    const cols = getFalseColor(edge.n0.networkDistance * 0.001);
                    ctx.strokeStyle = `rgba(${cols[0]}, ${cols[1]}, ${cols[2]}, 1.0) `
                    ctx.moveTo(edge.n0.point.x, edge.n0.point.y);
                    ctx.lineTo(edge.n1.point.x, edge.n1.point.y);
                    ctx.stroke();

                    const dis = edge.n0.point.distanceToGeo(edge.n1.point); // * 0.000621371;

                    if( !isPanning && this.gs.nnaEngine.app.controller.inspection){
                        ctx.font = "10px Arial";
                        ctx.fillStyle = '#000000';
                        ctx.fillText( `${dis.toFixed(2)}`, edge.n0.point.x + (edge.n1.point.x - edge.n0.point.x) * 0.5, edge.n0.point.y + (edge.n1.point.y - edge.n0.point.y) * 0.5);
                        // ctx.fillText( `${Math.round(edge.n0.networkDistance)}`, edge.n0.point.x + (edge.n1.point.x - edge.n0.point.x) * 0.5, edge.n0.point.y + (edge.n1.point.y - edge.n0.point.y) * 0.5  )
                        // ctx.fillText(`${Math.round(edge.n0.networkDistance)}`, edge.n1.point.x, edge.n1.point.y  )
                    }

            }
        }



        ctx.fillStyle = "#ffffff";
        // console.log(this.start.x, this.start.y);
        if(this.start) {
            ctx.beginPath();
            ctx.arc(this.start.x, this.start.y, 5, 0, Math.PI * 2.0);
            ctx.closePath();
            ctx.fill();
        }
        if(this.end) {
            ctx.beginPath();
            ctx.arc(this.end.x, this.end.y, 15, 0, Math.PI * 2.0);
            ctx.closePath();
            ctx.fill();
        }

        let selPathId = -1;
        let dis = Number.MAX_SAFE_INTEGER;
        const mx = this.gs.nnaEngine.app.controller.m.x;
        const my = this.gs.nnaEngine.app.controller.m.y;

        ctx.globalCompositeOperation = COLORBLENDING.COLOR_DODGE;
        for(let j = 0 ; j < this.path.length; ++j) {
            ctx.strokeStyle = 'rgba(255, 255, 10, 1)'
            ctx.lineWidth = 2.4;
            ctx.setLineDash([5, 4]);
            ctx.beginPath();
            ctx.moveTo(this.path[j][0].point.x, this.path[j][0].point.y);
            let theDis = distanceToXY(this.path[j][0].point.x, this.path[j][0].point.y, mx, my)
            if(theDis < dis) { dis = theDis; selPathId = j; }
            for(let i = 1 ; i < this.path[j].length; ++i) {

                if(this.path[j][i]){
                    ctx.lineTo(this.path[j][i].point.x, this.path[j][i].point.y);
                    theDis = distanceToXY(this.path[j][i].point.x, this.path[j][i].point.y, mx, my)
                    if(theDis < dis) { dis = theDis; selPathId = j; }
                }
            }

            
            // ctx.closePath();
            ctx.stroke();
            ctx.lineDashOffset = this.time * -5;
            ctx.setLineDash([]);
        }

        if(this.path.length > 0 && dis < 10) {
            this.selPath = this.path[selPathId];
            ctx.beginPath();
            ctx.moveTo(this.selPath[0].point.x, this.selPath[0].point.y);
            for(let i = 1 ; i < this.selPath.length; ++i) {
                if(this.selPath[i]){
                    ctx.lineTo(this.selPath[i].point.x, this.selPath[i].point.y);
                }
            }
            ctx.lineWidth = 4.4;
            ctx.stroke();
            ctx.lineDashOffset = this.time * -5;
            ctx.setLineDash([]);
        } else {
            this.selPath = undefined;
        }

        ctx.globalCompositeOperation = COLORBLENDING.SOURCE_OVER;
        // console.log(this.gs.graphCore.trees)

        this.time+=0.1;


        if(this.BoidsBehaviors) {
            this.BoidsBehaviors.Update();
            this.BoidsBehaviors.Render(ctx);
        }
    }
    public getPath(x: number, y: number) {
        if(this.selPath) {
        //     console.log('detecting...', this.selPath);
        }
    }
}