import { COMMAND, MouseEventData } from '../../lib'
import { CommandBase } from './CommandBase';
import { ActiveCommandController } from '../';
import { CPoint } from '../../Geometry/Point';
export class CommandSelect extends CommandBase {
    constructor(activeCommandController: ActiveCommandController) {
        super(activeCommandController);
        this.type = COMMAND.SELECT;

    }
    public MouseLeftClick(e: MouseEventData) {
    //     console.log('sel' , e)
    }
}
export class CommandPoint extends CommandBase {
    constructor(activeCommandController: ActiveCommandController) {
        super(activeCommandController);
        this.type = COMMAND.POINT;

    }
    public MouseLeftClick(e: MouseEventData) {
        const p = new CPoint(this.activeCommandController.drawingEngine.scene, {x: e.x, y: e.y, z : 0})
        const mv = this.activeCommandController.drawingEngine.projection.toMap(e.x, e.y);
        p.v.long = mv[0];
        p.v.lat = mv[1];

        this.activeCommandController.drawingEngine.scene.addGeometry(p);
        // console.log(this.activeCommandController.hasShift)
        if(this.activeCommandController.hasShift) { return }
        this.activeCommandController.drawingEngine.setCommand();
    }
}
