
import { MouseEventData } from './lib';

import { ActiveCommandController } from './ActiveCommand';
import { Projection } from './Projection';
import { IO } from './IO/IO';
import { GeometryCommon } from './Geometry/GeometryCommon';
import { Addon } from './Addon';
import { Renderer } from './Renderer';
import { Scene } from './Scene';
import { KeyboardEventData } from '../../NJSCore';

// import { tensor2d, tidy, Tensor2D } from '@tensorflow/tfjs';
// import * as tf from '@tensorflow/tfjs';


export class DrawingEngine {

    public activeCommand: ActiveCommandController;
    public projection: Projection;
    public geometryCommon: GeometryCommon;
    public scene: Scene;
    public addon: Addon;
    public renderer: Renderer;
    public io: IO;

    _ableToPan: boolean = true;

    constructor() {
        this.io = new IO(this);
        this.addon = new Addon(this);
        this.projection = new Projection(this);
        this.geometryCommon = new GeometryCommon(this);
        this.activeCommand = new ActiveCommandController(this);
        this.scene = new Scene(this);
        this.renderer = new Renderer(this);
    }

    public apiCall(command: string): any {
        return this.activeCommand.apiCall(command);
    }
    public updateProjection(projection: { toMap: (x: number, y: number) => number[] , toScreen: (x: number, y: number) => number[]} ){
        this.projection.updateProjection(projection);
    }
    public setCommand(command: string = 'SELECT') {
    //     console.log(command);
        this.activeCommand.setCommander(command);
    }
    public mouseEvent(m: MouseEventData){
        this.activeCommand.activeCommand.mouseEvent(m);

        this.addon.mouseInteraction(m);
    }
    public keyEvent(m: KeyboardEventData){
        // this.activeCommand.activeCommand.k(m);
    }
    public render(ctx: CanvasRenderingContext2D, isPanning: boolean) {
        this.renderer.render(ctx, isPanning);

        this.addon.render(ctx, isPanning);
    }
    public set ableToPan(v: boolean) {
        this._ableToPan = v;
        this.bindAbleToPan(v);
        // this.definition.app.ableToPan = v;
    }
    public bindAbleToPan(v: boolean){

    }
}