import { NVector3, NLine } from '../../../../lib';
import { Edge, Node } from '.'
import { Tree } from '../Tree/Tree';
import { GraphCore } from '..';

export class GraphData {
    public nodes: Node[] = [];
    public edges: Edge[] = [];
    public minPt: NVector3 = new NVector3(Number.MAX_VALUE, Number.MAX_VALUE, Number.MAX_VALUE);
    public maxPt: NVector3 = new NVector3(Number.MIN_VALUE, Number.MIN_VALUE, Number.MIN_VALUE);
}


export class Graph {
    public graphCore: GraphCore
    public nodes: Node[] = [];
    public edges: Edge[] = [];
    public minPt: NVector3 = new NVector3(Number.MAX_VALUE, Number.MAX_VALUE, Number.MAX_VALUE);
    public maxPt: NVector3 = new NVector3(Number.MIN_VALUE, Number.MIN_VALUE, Number.MIN_VALUE);


    public nodeOrigin: Node;

    public constructor(graphCore: GraphCore) {
        this.graphCore = graphCore;
    }
    public initGraph(lns: NLine[]) {
        this.nodes = [];
        this.edges = [];
        this.AddEdgeByLines(lns);
        return true;
    }
    public AddEdgeByLines(lns: NLine[]) {
        let nodes: Node[] = [];
        for (let i = 0; i < lns.length; ++i) {
            let p0: NVector3 = lns[i].p0;
            let p1: NVector3 = lns[i].p1;

            // bounding box
            if (this.minPt.long > p0.long) { this.minPt.long = p0.long; }
            if (this.minPt.lat > p0.lat) { this.minPt.lat = p0.lat; }
            if (this.minPt.elevation > p0.elevation) { this.minPt.elevation = p0.elevation; }
            if (this.maxPt.long < p0.long) { this.maxPt.long = p0.long; }
            if (this.maxPt.lat < p0.lat) { this.maxPt.lat = p0.lat; }
            if (this.maxPt.elevation < p0.elevation) { this.maxPt.elevation = p0.elevation; }

            if (this.minPt.long > p1.long) { this.minPt.long = p1.long; }
            if (this.minPt.lat > p1.lat) { this.minPt.lat = p1.lat; }
            if (this.minPt.elevation > p1.elevation) { this.minPt.elevation = p1.elevation; }
            if (this.maxPt.long < p1.long) { this.maxPt.long = p1.long; }
            if (this.maxPt.lat < p1.lat) { this.maxPt.lat = p1.lat; }
            if (this.maxPt.elevation < p1.elevation) { this.maxPt.elevation = p1.elevation; }

            nodes.push(this.AddNode(p0));
            nodes.push(this.AddNode(p1));
        }

        // let svec = this.graphCore.gs.nnaEngine.app.definition.app.projection.ToScreen(this.minPt.long, this.minPt.lat);
        // this.minPt.x = svec[0];
        // this.minPt.y = svec[1];
        // svec = this.graphCore.gs.nnaEngine.app.definition.app.projection.ToScreen(this.maxPt.long, this.maxPt.lat);
        // this.maxPt.x = svec[0];
        // this.maxPt.y = svec[1];

        for (let i = 0; i < nodes.length; i += 2) {
            this.AddEdge(nodes[i], nodes[i + 1]);
        }
    }
    public AddEdge(n1: Node, n2: Node): Edge | undefined {
        if (n1 === n2) return undefined;

        const testedEdge = this.FindEdge(n1, n2);
        if (testedEdge !== undefined) {
            return testedEdge;
        }

        let edge = new Edge(n1, n2);
        edge.id = this.edges.length;
        this.edges.push(edge);
        return edge;
    }
    public AddNode(p: NVector3): Node {
        for (let i = 0; i < this.nodes.length; ++i) {
            if (p.distanceTo(this.nodes[i].point) < 0.1) {
                return this.nodes[i];
            }
        }
        let node = new Node(p);
        node.id = this.nodes.length;
        this.nodes.push(node);
        return node;
    }

    // ..............................................................

    public FindEdge(n0: Node, n1: Node): Edge | undefined {
        for (let i = 0; i < this.edges.length; ++i) {
            if ((this.edges[i].n0 === n0 && this.edges[i].n1 === n1) || (this.edges[i].n0 === n1 && this.edges[i].n1 === n0)) {
                return this.edges[i];
            }
        }
        return undefined;
    }
    /*
    public Node FindClosestNode(Point3d p) {
        if (nodes.Count === 0) return null;
        let index = -1;
        double disMin = double.MaxValue;
        for (let i = 0; i < this.nodes.length; ++i) {
            double d = this.nodes[i].point.distanceTo(p);
            if (d < disMin) {
                index = i;
                disMin = d;
            }
        }
        return this.nodes[index];
    }
    */
    public FindClosestNode(p: NVector3): Node | undefined {
        if (this.nodes.length === 0) return undefined;
        let index = -1;
        let disMin = Number.MAX_VALUE;
        for (let i = 0; i < this.nodes.length; ++i) {
            let d = this.nodes[i].point.distanceTo(p);
            if (d < disMin) {
                index = i;
                disMin = d;
            }
        }
        return this.nodes[index];
    }
    public updatePropagation(originTree: Tree[], Optimizer: (current: Node, np: Node, Beta: number) => number, Beta: number) {
        for (let i = 0; i < this.nodes.length; ++i) {
            this.nodes[i].networkDistance = -1;
            this.nodes[i].euclideanDistance = -1;
        }

        for (let i = 0; i < originTree.length; ++i) {
            let n: Node = this.nodes[originTree[i].nodeOrigin.id];
            n.networkDistance = 0.0;
            n.euclideanDistance = 0.0;
            this.updatePropagationByNode(n, Optimizer, Beta);
        }

    }
    public updatePropagationByNodes(origins: Node [], Optimizer: (current: Node, np: Node, Beta: number) => number, Beta: number) { //, PixelMap pMap, transitionCostDelegate cost) {
        // this.Optimizer = Optimizer;
 
        // this.nodeOrigin = origin;

    //     console.log(origins);
 
        for(let i = 0; i < this.nodes.length; ++i) { this.nodes[i].reset(); }
 
        for(let i = 0 ; i < origins.length; ++i) {
            let origin = origins[i];
            origin.networkDistance = 0.0;
            origin.euclideanDistance = 0.0;
            let queue: Node[] = [];
            queue.push(origins[i]);
            
            for (let i = 0; i < 1000000; ++i) {
                if (queue.length === 0) { break; }
                let current: Node = queue[0];
                let dis = current.point.distanceToGeo(origin.point);
                if (current.euclideanDistance === -1 || dis < current.euclideanDistance) {
                    current.euclideanDistance = current.point.distanceToGeo(origin.point);
                }
    
                queue.shift();
                for (let j = 0; j < current.nodes.length; ++j) {
                    let np: Node = current.nodes[j];
                    let newcost = Optimizer(current, np, Beta);
                    let result = (current.networkDistance + newcost);
                    let newtemp = result; /// this.UNIT_VALUE; // result < 0.001 ? result  : result / this.UNIT_VALUE;
                    if (np.networkDistance === -1.0 || newtemp < np.networkDistance) {
                        np.networkDistance = newtemp;
                        queue.push(np);
                    }
                }
            }
        }
     }
    public updatePropagationByNode(origin: Node, Optimizer: (current: Node, np: Node, Beta: number) => number, Beta: number) { //, PixelMap pMap, transitionCostDelegate cost) {
       // this.Optimizer = Optimizer;

       this.nodeOrigin = origin;

       for(let i = 0; i < this.nodes.length; ++i) { this.nodes[i].reset(); }

        let queue: Node[] = [];
        queue.push(origin);
        for (let i = 0; i < 1000000; ++i) {
            if (queue.length === 0) { break; }
            let current: Node = queue[0];
            let dis = current.point.distanceToGeo(origin.point);
            if (current.euclideanDistance === -1 || dis < current.euclideanDistance) {
                current.euclideanDistance = current.point.distanceToGeo(origin.point);
            }

            queue.shift();
            for (let j = 0; j < current.nodes.length; ++j) {
                let np: Node = current.nodes[j];
                let newcost = Optimizer(current, np, Beta);
                let result = (current.networkDistance + newcost);
                let newtemp = result; /// this.UNIT_VALUE; // result < 0.001 ? result  : result / this.UNIT_VALUE;
                if (np.networkDistance === -1.0 || newtemp < np.networkDistance) {
                    np.networkDistance = newtemp;
                    queue.push(np);
                }
            }
        }
    }
    public updateBackpropagation(vs: NVector3[]): Node[][] {
        

        let paths: Node[][] = [];
        let i = 0; let j = 0; let k = 0;

        for (i = 0; i < vs.length; ++i) {
            let destination: Node = this.FindClosestNode(vs[i]) as Node;
            // this.nodeDestinations[i] = destination;
            let path: Node[] = [];
            if (destination.networkDistance != -1.0) {
                path.push(this.nodes[destination.id]);
                let current: Node = destination;
                for (j = 0; j < 100000; ++j) {
                    let smallest: Node = current;
                    smallest.countVisit++;
                    for (k = 0; k < current.nodes.length; ++k) {
                        let np: Node = current.nodes[k];
                        if (np.networkDistance < smallest.networkDistance) {
                            smallest = np;
                        }
                    }
                    if (smallest === current) {
                        path.push(this.nodeOrigin); // start node
                        // path[0].euclideanDistance = path[0].point.distanceTo(this.nodeOrigin.point);
                        paths.push(path);
                        break;
                    }
                    current = smallest;
                    path.push(this.nodes[current.id]);

                    // if (current === this.destination) { break; }
                }
            }

        }
        // this.paths = paths;
        return paths;
    }

}